



import echarts from "echarts";
import { Component, Prop, Vue } from "vue-property-decorator";
import axios from "axios";
@Component
export default class worldMap extends Vue {
  data: Array<any> = [];
  geoCoordMap: any = {};
  option: any = null;
  myChart: any = null;
  scatterData: any = [];
  effectScatterData1: any = [];
  effectScatterData2: any = [];
  loading:boolean = true;
  initData(data, geoCoordMap) {
    this.data = data;
    this.geoCoordMap = geoCoordMap;
    let arr = data.data1.concat(data.data2.concat(data.data3))
    this.registerMap();
    this.scatter(arr);
  }
  registerMap() {
    axios.get("./world.json").then((res: any) => {
      this.$echarts.registerMap("world", res.data);
      this.initMap();
      this.loading = false;
    });
  }
  scatter(arr: Array<any>) {
    this.scatterData = arr.map(item => {
      let value = this.geoCoordMap[item.name]
      // console.error('value',value);
      value.push(item.value)
      return {
        name: item.name,
        value:value,
        // value: [...this.geoCoordMap[item.name], item.value],
        itemStyle: {
          normal: {
            color: "#fff"
          }
        }
      };
    });
  }
  initMap() {
    this.myChart = this.$echarts.init(this.$el);
    var This = this;
    // var data1 = this.data.slice(0, 10);
    // var data2 = this.data.slice(10, 20);
    // var data3 = this.data.slice(20, 30);
    var data1 = (this as any).data.data1;
    var data2 = (this as any).data.data2;
    var data3 = (this as any).data.data3;

    this.option = {
      geo: {
        name: "world",
        type: "map",
        map: "world",
        roam: false,
        zoom:1.2,
        center: [0, 30],
        // aspectScale:0.7,
        itemStyle: {
          normal: {
            areaColor: "#0084cc",
            borderColor: "#48b2da"
          },
          emphasis: {
            areaColor: "#2a333d"
          }
        }
      },
      series: [
        // {
        //   type: "map",
        //   mapType: "world",
        //   zoom: 1.2,
        //   center: [0, 30],
        //   itemStyle: {
        //     emphasis: {
        //       areaColor: "#0057a6"
        //     },
        //     normal: {
        //       areaColor: "#0084cc",
        //       borderColor: "#48b2da"
        //     }
        //   },
        //   label: {
        //     normal: {
        //       show: false
        //     }
        //   }
        // },
        {
          type: "scatter",
          coordinateSystem: "geo",
          data: this.scatterData,
          label: {
            normal: {
              show: false,
              position: "right",
              color: "#fff",
              formatter: "{b}",
              textStyle: {
                color: "#fff",
                padding: [0, 10]
              }
            },
            emphasis: {
              show: true
            }
          }
        },
        {
          type: "effectScatter",
          coordinateSystem: "geo",
          zlevel: 2,
          rippleEffect: {
            //涟漪特效
            period: 2, //动画时间，值越小速度越快
            brushType: "stroke", //波纹绘制方式 stroke, fill
            scale: 5 //波纹圆环最大限制，值越大波纹越大
          },
          label: {
            normal: {
              show: true,
              position: "right", //显示位置
              offset: [5, 0], //偏移设置
              formatter: "{b}" //圆环显示文字
            },
            emphasis: {
              show: true
            }
          },
          symbol: "circle",
          symbolSize: 15,
          itemStyle: {
            normal: {
              show: false
            }
          },
          data: data1.map(function(item) {
            return {
              value: [...This.geoCoordMap[item.name], item.value],
              itemStyle: {
                normal: {
                  color: "#fff"
                }
              }
            };
          })
        },
        {
          type: "effectScatter",
          coordinateSystem: "geo",
          zlevel: 2,
          rippleEffect: {
            //涟漪特效
            period: 4, //动画时间，值越小速度越快
            brushType: "stroke", //波纹绘制方式 stroke, fill
            scale: 5 //波纹圆环最大限制，值越大波纹越大
          },
          label: {
            normal: {
              show: true,
              position: "right", //显示位置
              offset: [5, 0], //偏移设置
              formatter: "{b}" //圆环显示文字
            },
            emphasis: {
              show: true
            }
          },
          symbol: "circle",
          symbolSize: 15,
          itemStyle: {
            normal: {
              show: false
            }
          },
          // data: data2
          data: data2.map(function(item) {
            return {
              value: [...This.geoCoordMap[item.name], item.value],
              itemStyle: {
                normal: {
                  color: "#fff"
                }
              }
            };
          })
        },
        {
          type: "effectScatter",
          coordinateSystem: "geo",
          zlevel: 2,
          rippleEffect: {
            //涟漪特效
            period: 6, //动画时间，值越小速度越快
            brushType: "stroke", //波纹绘制方式 stroke, fill
            scale: 5 //波纹圆环最大限制，值越大波纹越大
          },
          label: {
            normal: {
              show: true,
              position: "right", //显示位置
              offset: [5, 0], //偏移设置
              formatter: "{b}" //圆环显示文字
            },
            emphasis: {
              show: true
            }
          },
          symbol: "circle",
          symbolSize: 15,
          itemStyle: {
            normal: {
              show: false
            }
          },
          // data: data2
          data: data3.map(function(item) {
            return {
              value: [...This.geoCoordMap[item.name], item.value],
              itemStyle: {
                normal: {
                  color: "#fff"
                }
              }
            };
          })
        }
      ]
    };
    this.myChart.setOption(this.option, true);
  }
  mounted() {
    // this.registerMap();
    // setTimeout(() => {
    //   this.initMap();
    // }, 2000);
  }
}
