




import echarts from "echarts";
import { Component, Prop, Vue } from "vue-property-decorator";

@Component
export default class barThree extends Vue {
  myChart: any = null;
  option: any = null;
  max: any = 0;
  fontSizeOne: number = 0.28 * (this as any).$rem.rem();
  data: Array<any> = [
    { name: "张三", value: 50 },
    { name: "李四", value: 60 },
    { name: "王五", value: 70 },
    { name: "赵六", value: 80 },
    { name: "陈七", value: 90 }
  ];
  colorArray: Array<any> = [
    "rgba(0,169,208,1)",
    "rgba(12,213,132,1)",
    "rgba(231,248,4,1)",
    "rgba(255,185,1,1)",
    "rgba(254,59,0,1)"
  ];
  mounted() {
    //   this.initBar();
  }
  listenScreen() {
    window.addEventListener("resize", () => {
      this.myChart.resize();
    });
  }
  initData(data: Array<any>) {
    if (data.length != 0) {
      this.data = data.map((item: any) => {
        return { name: item.name, value: parseFloat(item.value) };
      });
    }

    let value = this.data.map((item: any) => {
      return parseFloat(item.value);
    });
    this.max = Math.max(...value);
    this.initBar();
  }
  initBar() {
    this.listenScreen();
    this.myChart = this.$echarts.init(this.$el);

    this.option = {
      backgroundColor: "transparent",
      grid: {
        show: false,
        left: "0%",
        top: "0%",
        bottom: "0",
        right: "60%"
      },
      legend: {
        show: false,
        orient: "vertical",
        bottom: -10,
        icon: "circle",
        x: "center",
        formatter: (params: any, ticket: any, callback: any) => {
          console.error("响应时间图例---", params, ticket, callback);
        },
        data: [
          {
            name: "外圆",
            icon: "circle",
            textStyle: {
              color: "red"
            }
          },
          {
            name: "bar",
            icon: "circle",
            textStyle: {
              color: "red"
            }
          }
        ],
        right: -50,
        padding: [30, 60],
        itemGap: 0.2 * this.$rem.rem(),
        itemWidth: 0.45 * this.$rem.rem(),
        itemHeight: 0.24 * this.$rem.rem()
      },
      polar: {
        center: ["40%", "50%"]
      },
      angleAxis: {
        show: false,
        min: 0,
        max: this.max + 10
      },
      radiusAxis: {
        type: "category",
        axisLabel: {
          show: false,
          interval: 0,
          color: "#2df",
          fontSize: 16
        },
        axisLine: {
          show: false
        },
        axisTick: {
          show: false,
          alignWithLabel: true
        },
        splitLine: {
          show: true,
          lineStyle: {
            color: "rgba(49,71,100,1)"
          }
        },
        data: this.data.map(function(item) {
          return item.name;
        })
      },
      series: [
        {
          name: "bar",
          type: "bar",
          coordinateSystem: "polar",
          barWidth: 4,
          itemStyle: {
            color: params => {
              return this.colorArray[params.dataIndex];
            },
            barBorderRadius: 10,
            shadowBlur: 20,
            shadowColor: "rgba(255, 255, 255, 0.8)"
          },
          emphasis: {
            itemStyle: {
              shadowColor: "rgba(255, 255, 255, 1)"
            }
          },
          data: this.data
        },
        {
          name: "外圆",
          type: "scatter",
          coordinateSystem: "polar",
          hoverAnimation: false,
          data: [0, 0, 0, 0, 0],
          yAxisIndex: 2,
          symbolSize: 10,
          itemStyle: {
            normal: {
              color: params => {
                return this.colorArray[params.dataIndex];
              },
              opacity: 1
            }
          },
          z: 2
        }
        // {
        //     type: "custom",
        //     coordinateSystem: 'polar',
        //     renderItem: function(params, api) {
        //         var values = [api.value(0), api.value(1)];
        //         var coord = api.coord(values);
        //         return {
        //             type: 'text',
        //             position: [3 * Math.sin(coord[3]), 3 * Math.cos(coord[3])],
        //             rotation: coord[3] + Math.PI / 2,
        //             origin: [coord[0], coord[1]],
        //             style: {
        //                 text: api.value(1),
        //                 fill: "#ac6",
        //                 fontSize: 16,
        //                 textAlign: "right",
        //                 textVerticalAlign: "middle",
        //                 x: coord[0],
        //                 y: coord[1]
        //             }
        //         };
        //     },
        //     data: this.data
        // }
      ]
    };
    this.myChart.setOption(this.option, true);
  }
}
