




import { Component, Prop, Vue } from "vue-property-decorator";

@Component
export default class Pie extends Vue {
  myChart: any = null;
  option: any = null;

  fontSizeOne: number = 0.28 * (this as any).$rem.rem();
  mounted() {
    // this.listenScreen();
    // this.initPie();
  }
  listenScreen() {
    window.addEventListener("resize", () => {
      this.myChart.resize();
      // setTimeout(this.initPie, 50);
    });
  }
  initPie(data: Array<any>) {
    this.listenScreen();
    this.myChart = this.$echarts.init(this.$el);
    let values = data.map((item: any) => {
      return item.value;
    });
    let total = values.reduce((p, f) => {
      return p + f;
    });
    this.option = {
      background: "transparent",
      title: {
        text: total,
        subtext: "总数",
        x: "center",
        y: "center",
        textStyle: {
          color: "#2a9be4",
          fontSize: 0.72 * this.$rem.rem(),
          fontWeight: "lighter",
          fontFamily: "MicrosoftYaHei"
        },
        subtextStyle: {
          color: "#a2c1f5",
          fontSize: 0.24 * this.$rem.rem()
        },
        itemGap: 0
      },
      tooltip: {
        trigger: "item",
        backgroundColor: "rgba(0,0,0,0.4)",
        formatter: "{b}: {c} ({d}%)"
      },
      legend: {
        show: true,
        orient: "vertical",
        top: "middle",
        textStyle: {
          color: "#9ec0db",
          fontSize: this.fontSizeOne,
          padding: [0, 15]
        },
        icon: "rect",
        // right: 0.1 * this.$rem.rem(),
        right: -50,
        padding: [30, 60],
        itemGap: 0.2 * this.$rem.rem(),
        itemWidth: 0.45 * this.$rem.rem(),
        itemHeight: 0.24 * this.$rem.rem()
      },
      series: [
        {
          type: "pie",
          center: ["50%", "50%"],
          radius: ["50%", "65%"],
          color: [
            "#da3225",
            "#df7814",
            "#ddd10d",
            "#1a9ff5",
            "#76dc79",
            "#b5b5b5",
            "#535353"
          ],
          startAngle: 135,
          labelLine: {
            normal: {
              length: 25
            }
          },
          itemStyle: {
            normal: {
              borderColor: "#061F3D",
              borderWidth: 3
            }
          },
          label: {
            normal: {
              show: false,
              formatter: "{b|{b}:}  {per|{d}%} ",
              backgroundColor: "rgba(255, 147, 38, 0)",
              borderColor: "transparent",
              borderRadius: 4,
              rich: {
                a: {
                  color: "#999",
                  lineHeight: 22,
                  align: "center"
                },
                hr: {
                  borderColor: "#aaa",
                  width: "100%",
                  borderWidth: 1,
                  height: 0
                },
                b: {
                  color: "#b3e5ff",
                  fontSize: 16,
                  lineHeight: 33
                },
                c: {
                  fontSize: 14,
                  color: "#eee"
                },
                per: {
                  color: "#FDF44E",
                  fontSize: 14,
                  padding: [5, 8],
                  borderRadius: 2
                }
              },
              textStyle: {
                color: "#fff",
                fontSize: 16
              }
            }
          },
          emphasis: {
            label: {
              show: false,
              position: "center",
              formatter: "{b|{b}:}  {per|{d}%}  ",
              backgroundColor: "rgba(255, 147, 38, 0)",
              borderColor: "transparent",
              borderRadius: 4,
              rich: {
                a: {
                  color: "#999",
                  lineHeight: 22,
                  align: "center"
                },
                hr: {
                  borderColor: "#aaa",
                  width: "100%",
                  borderWidth: 1,
                  height: 0
                },
                b: {
                  color: "#fff",
                  fontSize: 0.18 * (this as any).$rem.rem(),
                  lineHeight: 33
                },
                c: {
                  fontSize: 14,
                  color: "#eee"
                },
                per: {
                  color: "#FDF44E",
                  fontSize: 0.25 * (this as any).$rem.rem(),
                  padding: [5, 6],
                  borderRadius: 2
                }
              }
            }
          },
          data: data
        },
        {
          type: "pie",
          center: ["50%", "50%"],
          radius: ["40%", "41%"],
          label: {
            show: false
          },
          data: [
            {
              value: 78,
              itemStyle: {
                normal: {
                  color: {
                    type: "radial",
                    x: 0.5,
                    y: 0.5,
                    r: 0.5,
                    colorStops: [
                      {
                        offset: 0,
                        color: "#0497FE" // 0% 处的颜色
                      },
                      {
                        offset: 0.3,
                        color: "#0497FE" // 0% 处的颜色
                      },
                      {
                        offset: 1,
                        color: "rgba(21,153,246,0)" // 100% 处的颜色
                      }
                    ],
                    global: false // 缺省为 false
                  },
                  borderWidth: 1,
                  shadowBlur: 30,
                  borderColor: "#0497FE",
                  shadowColor: "#0497FE"
                }
              }
            }
          ]
        }
      ]
    };

    this.myChart.setOption(this.option, true);
  }
}
