




import { Component, Prop, Vue } from "vue-property-decorator";

@Component
export default class Pie extends Vue {
  myChart: any = null;
  option: any = null;
  value: Array<any> = [
    { name: "紧急", value: 2 },
    { name: "重要", value: 5 },
    { name: "次要", value: 3 }
  ];
  total: any = 0;
  color: Array<any> = ["#da3225", "#df7814", "#ddd10d"];
  fontSizeOne: number = 0.28 * (this as any).$rem.rem();
  mounted() {
    this.listenScreen();
  }
  listenScreen() {
    window.addEventListener("resize", () => {
      this.myChart.resize();
    });
  }
  initData(data: Array<any>) {
    if (data.length != 0) {
      this.value = data;
    }
    this.initLegendData(this.value);
    let values = this.value.map((item: any) => {
      return item.value;
    });
    let total = values.reduce((p, f) => {
      return p + f;
    });

    this.initPie();
  }
  initLegendData(data: Array<any>) {
    return data.map((item, index) => {
      return { textStyle: { color: this.color[index] } };
    });
  }
  initPie() {
    this.listenScreen();
    this.myChart = this.$echarts.init(this.$el);
    this.option = {
      background: "transparent",
      grid: {
        left: "0%",
        right: "0%",
        bottom: "0%",
        top: "0%"
      },
      title: {
        show: false,
        text: this.total,
        subtext: "总数",
        x: "center",
        y: "center",
        textStyle: {
          color: "#2a9be4",
          fontSize: 0.72 * this.$rem.rem(),
          fontWeight: "lighter",
          fontFamily: "MicrosoftYaHei"
        },
        subtextStyle: {
          color: "#a2c1f5",
          fontSize: 0.24 * this.$rem.rem()
        },
        itemGap: 0
      },
      tooltip: {
        trigger: "item",
        backgroundColor: "rgba(0,0,0,0.4)",
        formatter: "{b}: {c} ({d}%)"
      },
      legend: {
        show: true,
        orient: "horizontal",
        bottom: -10,
        icon: "circle",
        // data: legendData,
        formatter: name => {
          console.log("legend-----", name);
          let choosenData = this.value.find(item => {
            return item.name == name;
          });
          let value = choosenData == undefined ? 0 : choosenData.value;
          return ["{a|" + name + "}", "{b|" + value + "}"].join("\r");
        },
        textStyle: {
          color: "#9ec0db",
          padding: [0, 20, 0, 0],
          rich: {
            a: {
              color: "#9ec0db",
              lineHeight: 10,
              fontSize: 0.32 * this.$rem.rem()
            },
            b: {
              color: "rgb(3,231,239)",
              fontSize: 0.54 * this.$rem.rem()
            }
          }
        },
        right: -50,
        padding: [30, 60],
        itemGap: 0.2 * this.$rem.rem(),
        itemWidth: 0.45 * this.$rem.rem(),
        itemHeight: 0.24 * this.$rem.rem()
      },
      series: [
        {
          type: "pie",
          center: ["50%", "50%"],
          radius: ["25%", "65%"],
          roseType: "radius",
          color: this.color,
          startAngle: 135,
          labelLine: {
            normal: {
              show: true,
              length: 25
            }
          },
          itemStyle: {
            normal: {
              borderColor: "#061F3D",
              borderWidth: 0
            }
          },
          label: {
            normal: {
              show: true,
              position: "outside",
              formatter: "{per|{d}%}",
              backgroundColor: "rgba(255, 147, 38, 0)",
              borderColor: "transparent",
              borderRadius: 4,
              rich: {
                a: {
                  color: "#999",
                  lineHeight: 22,
                  align: "center"
                },
                hr: {
                  borderColor: "#aaa",
                  width: "100%",
                  borderWidth: 1,
                  height: 0
                },
                b: {
                  color: "#b3e5ff",
                  fontSize: 16,
                  lineHeight: 33
                },
                c: {
                  fontSize: 14,
                  color: "#eee"
                },
                per: {
                  color: "#FDF44E",
                  fontSize: 14,
                  padding: [5, 8],
                  borderRadius: 2
                }
              },
              textStyle: {
                color: "#fff",
                fontSize: 16
              }
            }
          },
          emphasis: {
            label: {
              show: true,
              position: "center",
              formatter: "{per|{d}%}  ",
              backgroundColor: "rgba(255, 147, 38, 0)",
              borderColor: "transparent",
              borderRadius: 4,
              rich: {
                a: {
                  color: "#999",
                  lineHeight: 22,
                  align: "center"
                },
                hr: {
                  borderColor: "#aaa",
                  width: "100%",
                  borderWidth: 1,
                  height: 0
                },
                b: {
                  color: "#fff",
                  fontSize: 0.18 * (this as any).$rem.rem(),
                  lineHeight: 33
                },
                c: {
                  fontSize: 14,
                  color: "#eee"
                },
                per: {
                  color: "#FDF44E",
                  fontSize: 0.25 * (this as any).$rem.rem(),
                  padding: [5, 6],
                  borderRadius: 2
                }
              }
            }
          },
          data: this.value
        },
        {
          type: "pie",
          center: ["50%", "50%"],
          radius: ["13%", "15%"],
          silent: true,
          label: {
            show: false
          },
          data: [
            {
              value: 78,
              itemStyle: {
                normal: {
                  color: {
                    type: "radial",
                    x: 0.5,
                    y: 0.5,
                    r: 0.5,
                    colorStops: [
                      {
                        offset: 0,
                        color: "rgb(0,129,145)" // 0% 处的颜色
                      },
                      {
                        offset: 1,
                        color: "rgb(0,129,145)" // 0% 处的颜色
                      }
                    ],
                    global: false // 缺省为 false
                  }
                }
              }
            }
          ]
        }
      ]
    };

    this.myChart.setOption(this.option, true);
  }
}
