




import { Component, Vue } from "vue-property-decorator";

@Component
export default class Clock extends Vue {
  currentTime: string = "";
  beforeCreate(){
    this['timer'] = null;
  }
  refreshTime() {
    clearTimeout((this as any).timer);
    this.currentTime = (this as any).$common.getCurrentDate(1);
    (this as any).timer = setTimeout(this.refreshTime, 1000);
  }
  mounted() {
    this.refreshTime();
  }
}
