//  引入axios文件包
import axios from 'axios'
import Vue from "vue";
//封装axios的接口请求，统一做异常处理与成功回调处理
//插件添加Vue实例方法
Vue.prototype.getApiData = function(method, url, params, successBack, errorBack) {
	//请求URL添加时间戳
	let startTime = new Date().getTime();

	if(url.indexOf("?") > -1) {
		url = url + "&timestamp=" + startTime;
	} else {
		url = url + "?timestamp=" + startTime;
	}
	axios[method](this.$httpUrl + url, params)
		.then((response) => {
			if(response.status == 200) { 
				successBack(response)
			} else {
//				this.$Message.error(
//					response.msg
//				)
				//传了errorBack函数,才会执行下面的代码
				errorBack && errorBack(response)
			}
		})
		.catch((reject) => {
			if(document.getElementsByClassName("ivu-message-notice").length == 0) {
				if(reject.response) {
//					this.$Message.error(reject.response.data.message)
				} else if(reject.message) {
//					this.$Message.error(reject.message)
				}
			}
		});
}