











import { Component, Vue, Prop } from "vue-property-decorator";
@Component
export default class AnimateList extends Vue {
  data: any = [];
  number: any = [0, 9];
  dataArr: any = [];
  saveData: any = [];
  timer:any  = "";
  // className: any = "animated zoomIn infinite animateBox slower";
  className: any = "animateBox animation";
  initData(data) {
    this.data = data;
    this.initFlag();
    //this.startTimer()
  }
  startTimer(){
   
    this.timer = setInterval(() => {
      //  console.error('this.number',this.number);
      let min = this.number[1] + 1;
      let max = this.number[1] + (this.number[1] - this.number[0]) + 1;
      this.number = [min, max];
      if (this.data.length > this.number[0]) {
        this.initFlag();
      } else {
        this.number = [0, 9];
        this.initFlag();
      }
    }, 5000);
  }
  initFlag() {

      this.data.forEach((item,index)=>{
        if (index >= this.number[0] && index <= this.number[1]) {
          this.$set(this.data[index], "flag", true);
        } else {
          this.$set(this.data[index], "flag", false);
        }

        this.$set(this.data[index], "styles", {
          "animation-delay": this.randomNumBoth(0, 2) + "s",
          top: this.randomNumBoth(0, 210) + "px",
          left: this.randomNumBoth(0, 190) + "px"
        });
      })

       

      this.$nextTick(()=>{
          this.animate();
      })

    
  }

  animate(){
        let divs = (document.querySelectorAll(".animateDiv") as any);    
          for(let i=0 ; i<divs.length; i++){
              divs[i].addEventListener('animationstart',()=>{
              })
              divs[i].addEventListener('mouseenter',()=>{
                  for(let idx=0 ; idx<divs.length; idx++){
                    divs[idx].style.animationPlayState = "paused";
                  }
                
              },false)
              divs[i].addEventListener('mouseleave',()=>{
                  for(let idx=0 ; idx<divs.length; idx++){
                    divs[idx].style.animationPlayState = "running";
                  }
                
              },false)
              
              divs[i].addEventListener('animationend',()=>{
                this.$set(this.data[i], "flag", false);
                divs[i].className=divs[i].className.replace("animation","");
                setTimeout(()=>{
                  this.$set(this.data[i], "styles", {
                    "animation-delay": this.randomNumBoth(0, 2) + "s",
                    top: this.randomNumBoth(0, 210) + "px",
                    left: this.randomNumBoth(0, 190) + "px"
                  });
                  this.$set(this.data[i], "flag", true);
                  
                  divs[i].className=divs[i].className.concat(" animation")
                },3000);

                if(i+10<this.data.length){
                  this.$set(this.data[i+10], "flag", true);
                  setTimeout(()=>{
                    this.$set(this.data[i+10], "flag", false);
                    divs[i+10].className=divs[i+10].className.replace("animation","");
                    setTimeout(()=>{
                      this.$set(this.data[i+10], "styles", {
                          "animation-delay": this.randomNumBoth(0, 2) + "s",
                          top: this.randomNumBoth(0, 200) + "px",
                          left: this.randomNumBoth(0, 190) + "px"
                        });
                        this.$set(this.data[i+10], "flag", true);
                        divs[i+10].className=divs[i+10].className.concat(" animation")
                    },3000)
                  },3000);
                }
              })
          }
  }
  randomNumBoth(Min, Max) {
    return Math.floor(Math.random() * (Max - Min) + Min);
  }
  mounted() {
    
  }
}
