





import { Component, Prop, Vue } from "vue-property-decorator";
@Component
export default class pictorialBar extends Vue {
  myChart: any = null;
  option: any = null;
  name: Array<any> = ["桃源街", "东海街", "柳林街", "陈塘街", "天塔街"];
  value: Array<any> = [29,40,45,88,100];
  type: string = "";
  fontSizeOne: number = 0.36 * this.$rem.rem();
  fontSizeTwo: number = 0.36 * this.$rem.rem();

  mounted() {
    this.init();
  }
  init() {
    window.addEventListener("resize", () => {
      console.error("rightFlow图监测到窗口发生了变化");
      setTimeout(() => {
        this.myChart.resize();
      }, 2000);
    });
  }
  initData(data: Array<any>) {
    if (data.length != 0) {
      this.name = data.map((item: any) => {
        return item.name;
      });
      this.value = data.map((item: any) => {
        return item.value;
      });
    }
    this.drawBar();
  }
  drawBar() {
    console.error("进入绘制rightFlow方法...");
    this.myChart = this.$echarts.init(this.$refs.flow);
    this.option = {
      background: "white",
      title: {
        show: false,
        text: "带宽占用率",
        left: "2%",
        y: "top",
        textStyle: {
          color: "#78d4ff",
          fontWeight: "lighter",
          fontSize: this.fontSizeOne
        }
      },
      grid: {
        left: "0%",
        top: "0%",
        bottom: "0",
        right: "20%",
        containLabel: true
      },
      legend: {
        show: false
      },
      xAxis: {
        max: 100,
        splitLine: {
          show: false
        },
        axisTick: {
          show: false
        },
        axisLine: {
          show: false
        },
        axisLabel: {
          show: false
        }
      },
      yAxis: {
        splitLine: {
          show: false
        },
        axisTick: {
          show: false
        },
        axisLine: {
          show: false
        },
        axisLabel: {
          color: "white",
          fontSize: this.fontSizeTwo,
          inside: false,
          rotate: 0,
          margin: 100,
          verticalAlign: "middle",
          align: "left",
          formatter: function(c) {
            let num = Math.ceil(c.length / 5);
            let remain = c.length % 5;
            let arr = [];
            for (let i = 0; i < num; i++) {
              arr.push(c.substring(i * 5, (i + 1) * 5));
            }
            if (num > 1 && remain != 0) {
              arr.push(c.substring(num * 5, num * 5 + remain));
            }
            return arr.length > 4
              ? arr.splice(0, 4).join("\n")
              : arr.join("\n");
          }
        },
        data: this.name
      },
      series: [
        {
          name: "数据",
          type: "pictorialBar",
          barWidth: "30",
          barGap: "-100%",
          barCategoryGap: "50%",
          //象形柱图的实际值
          data: this.value,
          z: 3,
          symbol: "image://colorBar.png",
          symbolClip: true,
          //象形柱图的最大值
          symbolBoundingData: [100, 100, 100, 100, 100],
          symbolSize: ["100%", "100%"],
          label: {
            normal: {
              show: true,
              position: "right",
              fontSize: this.fontSizeTwo,
              color: "rgb(5,251,254)",
              offset: [0, 0],
              formatter: param => {
                return param.value + "%";
              }
            }
          }
        }
      ]
    };
    this.myChart.setOption(this.option);
  }
}
