













import echarts from "echarts";
import { Component, Prop, Vue } from "vue-property-decorator";

@Component
export default class Bar extends Vue {
  myChart: any = null;
  option: any = null;
  value: Array<any> = [11];
  name: Array<any> = ["设备1"];
  data: any = { name: "VCPU出租率", value: 3545 };

  fontSizeOne: number = 0.28 * (this as any).$rem.rem();
  mounted() {}
  listenScreen() {
    window.addEventListener("resize", () => {
      this.myChart.resize();
    });
  }
  initData(data: any) {
    if (data.length != 0) {
      this.value = data.value;
      this.name = data.name;
      this.data = data.data;
    }
    (this as any).$refs.myCount.changeData(0, this.data.value);
    this.initBar();
  }
  initBar() {
    this.listenScreen();
    this.myChart = this.$echarts.init(this.$refs.barTwo);
    this.option = {
      grid: {
        show: false,
        left: "2%",
        top: "10%",
        bottom: "0",
        right: "10%"
      },
      backgroundColor: "transparent", //背景色
      xAxis: {
        show: false, //是否显示x轴
        type: "value",
        name: "VCPU出租率",
        nameLocation: "end"
      },
      yAxis: {
        show: true,
        type: "category",
        name: "VCPU出租率",
        nameLocation: "end",
        inverse: false, //让y轴数据逆向
        axisLabel: {
          show: false,
          textStyle: {
            color: "#fff" //y轴字体颜色
          },
          //定义富文本标签
          rich: {
            lg: {
              fontWeight: "bold",
              fontSize: 12, //字体默认12
              color: "#08C",
              padding: [0, 5, 0, 0]
            },
            title: {
              color: "#fff",
              fontWeight: "lighter"
            }
          }
        },
        splitLine: { show: false }, //横向的线
        axisTick: { show: false }, //y轴的端点
        axisLine: { show: false }, //y轴的线
        data: this.name
      },
      series: [
        {
          name: "数据内框",
          type: "bar",
          itemStyle: {
            normal: {
              barBorderRadius: 30,
              color: {
                type: "liner",
                x: 0.5,
                y: 0.5,
                r: 0.5,
                colorStops: [
                  {
                    offset: 0,
                    color: "rgb(9,150,222)" // 0% 处的颜色
                  },
                  {
                    offset: 1,
                    color: "rgb(0,250,248)" // 0% 处的颜色
                  }
                ],
                global: false // 缺省为 false
              }
            }
          },
          label: {
            normal: {
              show: true,
              position: "insideRight",
              color: "black",
              fontSize: 14,
              formatter: param => {}
            }
          },
          barWidth: 20,
          data: this.value
        },
        {
          name: "外框",
          type: "bar",
          itemStyle: {
            normal: {
              barBorderRadius: 30,
              color: "rgba(255, 255, 255, 0.14)" //rgba设置透明度0.14
            }
          },
          barGap: "-100%",
          z: 0,
          barWidth: 20,
          data: [100],
          label: {
            normal: {
              show: true,
              position: "right",
              color: "rgb(5, 200, 249)",
              fontSize: 14,
              formatter: param => {
                return this.value + "%";
              }
            }
          }
        }
      ]
    };
    this.myChart.setOption(this.option, true);
  }
}
