import Vue from "vue";
import App from "./App.vue";
import router from "./router";
import store from "./store";
import "@/assets/css/home.scss";
import "@/assets/css/third.scss";
import * as echarts from "echarts";

import "./components/componentRegister.ts";
import rem from './assets/js/rem';
import common from './assets/js/common';
import "./assets/js/request";
import './config';
import jq from 'jquery';


import { Switch,Message,Spin } from 'iview';
import 'iview/dist/styles/iview.css'

import scroll from 'vue-seamless-scroll'

import {Tooltip,Table,TableColumn,Carousel,CarouselItem,Dialog,Tree,Button,Popover,Loading} from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'

Vue.component(Tooltip.name,Tooltip);
Vue.component(Table.name,Table);
Vue.component(TableColumn.name,TableColumn);
Vue.component(Carousel.name,Carousel);
Vue.component(CarouselItem.name,CarouselItem);
Vue.component(Dialog.name,Dialog);
Vue.component(Tree.name,Tree);
Vue.component(Button.name,Button);
Vue.component(Popover.name,Popover);


Vue.component('i-switch', Switch);
Vue.component('Message',Message);
Vue.component('Spin',Spin);

Vue.use(scroll)
Vue.use(Loading.directive)



Vue.prototype.$loading = Loading.service
Vue.prototype.$Message=Message;
Vue.prototype.$echarts = echarts;
Vue.prototype.$rem = rem; 
Vue.prototype.$common = common;


declare module "vue/types/vue" {
  interface Vue {
    $httpUrl:string;
    $time:number;
    $title_1:string;
    $title_2:string;
    $title_3:string;
    $title_4:object;
    $echarts: any;
    $rem:any;
    $common:any;
    getApiData:Function;
    $Message: Message;
    $data_center:any;
    $business:any;
  }
}

Vue.config.productionTip = false;

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount("#app");
