export default {
	rem:function(){
		var designWidth = "3840";
		var screenWidth = document.documentElement.getBoundingClientRect().width;
		if(screenWidth<1920){
			screenWidth = 1920;
		}
		var rem = screenWidth * 100 / designWidth;
		document.documentElement.style.fontSize = rem + 'px';
		return rem;
	}
};