




import echarts from "echarts";
import { Component, Prop, Vue } from "vue-property-decorator";
import axios from "axios";
@Component
export default class areaMap extends Vue {
  
  beforeCreate(){
    //白色的点
    this['spots'] = [];
    
  //深蓝色和亮蓝色的点
  this['spotsScatter'] = [];
  //亮蓝色的点
  this['spotsLighter'] = [];
  this['dataCenter'] = [
    { name: "呼和浩特数据中心", value: [113.17, 41.81] },
    { name: "常州数据中心", value: [121.26, 31.54] },
    { name: "南昌数据中心", value: [116.89, 27.97] },
    { name: "北京", value: [117.4, 39.4] }
  ];
  this['dataLinesAbnormal'] = [];
  this['dataLinesNormal'] = [];
  this['data'] = [];
  this['option'] = null;
  this['myChart'] = null;
  this['myColor'] = {
    type: "radial",
    x: 0.5,
    y: 0.5,
    r: 0.5,
    colorStops: [
      {
        offset: 0,
        color: "rgba(6, 253, 2,1)"
      },
      {
        offset: 0.3,
        color: "rgba(6, 253, 2,1)"
      },
      {
        offset: 0.8,
        color: "transparent"
      },
      {
        offset: 0.801,
        color: "transparent"
      },
      {
        offset: 0.81,
        color: "rgba(6, 253, 2,0.2)"
      },
      {
        offset: 1,
        color: "rgba(6, 253, 2,0.4)"
      }
    ],
    global: false // 缺省为 false
  };
  this['coords'] = [];
  }
  initData(data: Array<any>, dataScatter: Array<any>, map: Array<any>) {
    (this as any).data = data;
    this.initSpots(data, dataScatter);
    this.initDataCenter();
    this.initLinesData(map);
  }
  initSpots(data: Array<any>, dataScatter: Array<any>) {
    console.error('开始处理数据-----',new Date().getMilliseconds());
    (this as any).spots = data;
    (this as any).spotsScatter = dataScatter.map((item, index) => {
      return {
        name: "",
        value: item,
        symbolSize: 12,
        label: {
          show: false,
          normal: {
            position: "right",
            textStyle: {
              fontSize: 11,
              color: "#fff"
            }
          }
        },
        itemStyle: {
          normal: {
            color: {
              type: "radial",
              x: 0.5,
              y: 0.5,
              r: 0.5,
              colorStops: [
                {
                  offset: 0,
                  color: "rgba(6,106,218,1)"
                },
                {
                  offset: 0.2,
                  color: "rgba(6,106,218,1)"
                },
                {
                  offset: 1,
                  color: "rgba(6,106,218,0)"
                }
              ]
            },
            opacity: 1
          }
        },
        emphasis: {
          itemStyle: {
            opacity: 1
          }
        }
      };
    });
    (this as any).spotsLighter = dataScatter.map((item, index) => {
      return {
        name: "",
        value: item,
        symbolSize: 12,
        label: {
          show: false,
          normal: {
            position: "right",
            textStyle: {
              fontSize: 11,
              color: "#fff"
            }
          }
        },
        itemStyle: {
          normal: {
            color:
              Math.random() < 0.7
                ? "transparent"
                : {
                    type: "radial",
                    x: 0.5,
                    y: 0.5,
                    r: 0.5,
                    colorStops: [
                      {
                        offset: 0,
                        color: "rgba(29,255,255,1)"
                      },
                      {
                        offset: 0.3,
                        color: "rgba(29,255,255,1)"
                      },
                      {
                        offset: 0.31,
                        color: "transparent"
                      },
                      {
                        offset: 1,
                        color: "rgba(29,255,255,0)"
                      }
                    ]
                  },

            opacity: 1
          }
        },
        emphasis: {
          itemStyle: {
            opacity: 1
          }
        }
      };
    });
    console.error('结束处理数据-----',new Date().getMilliseconds());
  }
  initDataCenter() {
    let positions = ["left", "right", "bottom", "right"];
    (this as any).dataCenter = (this as any).dataCenter.map((item, index) => {
      return {
        name: item.name,
        value: item.value,
        symbol: item.name == "北京" ? "image://star.png" : "circle",
        symbolSize: item.name == "北京" ? 100 : 30,
        label: {
          normal: {
            show: false
          },
          emphasis: {
            show: index == 3 ? false : true,
            position: positions[index],
            lineHeight: 17,
            fontSize: 14,
            formatter: (b, index) => {
              let num = Math.ceil(b.name.length / 4);
              let remain = b.name.length % 4;
              let arr = [];
              for (let i = 0; i < num; i++) {
                arr.push(b.name.substring(i * 4, (i + 1) * 4));
              }
              if (num > 2 && remain != 0) {
                arr.push(b.name.substring(num * 4, num * 4 + remain));
              }
              console.error(99999, arr);
              return arr.length > 4
                ? arr.splice(0, 4).join("\n")
                : arr.join("\n");
            },
            color: "#A0D9DB",
            padding: [10, 20],
            borderColor: "#179E3F",
            backgroundColor: "rgba(0,0,0,0.6)",
            borderWidth: 1
          }
        },
        itemStyle: {
          normal: {
            color: (this as any).myColor,
            borderColor: "rgba(6, 253, 2,1)"
          }
        },
        emphasis: {
          itemStyle: {
            opacity: 1
          }
        }
      };
    });
  }
  initLinesData(map: Array<any>) {
    let abnormal = [];
    let normal = [];
    (this as any).coords = (this as any).dataCenter.map(item => {
      return item.value;
    });
    (this as any).dataCenter.forEach((item, index) => {
      let temp = (this as any).dataCenter.forEach((item2, index2) => {
        let state = map.find(item3 => {
          return item3.from == item.name && item3.to == item2.name;
        }).state;
        if (state == 1) {
          abnormal.push([
            {
              coord: item.value,
              lineStyle: {
                color: "#FF0000",
                width: 1.5,
                curveness: -0.3
              }
            },
            {
              coord: item2.value
            }
          ]);
        } else {
          normal.push([
            {
              coord: item.value,
              lineStyle: {
                color: "#07FD4F",
                width: 1.5,
                curveness: -0.3
              }
            },
            {
              coord: item2.value
            }
          ]);
        }
      });
      (this as any).dataLinesAbnormal = abnormal;
      (this as any).dataLinesNormal = normal;
      this.initMap();
    });
    console.error("dataLinesAbnormal-----", (this as any).dataLinesAbnormal);
  }
  registerMap() {
    axios.get("./china.json").then((res: any) => {
      this.$echarts.registerMap("hexi", res.data);
    });
  }
  initMap() {
    if (null == (this as any).myChart) {
      (this as any).myChart = this.$echarts.init(this.$el);
    }
    (this as any).option = {
      grid: {
        show: false,
        top: 0,
        bottom: 0,
        right: 0,
        left: 0
      },
      xAxis: [
        {
          gridIndex: 0,
          type: "value",
          show: false,
          min: 0,
          max: 100,
          nameLocation: "middle",
          nameGap: 5
        }
      ],
      yAxis: [
        {
          gridIndex: 0,
          min: 0,
          show: false,
          max: 100,
          nameLocation: "middle",
          nameGap: 30
        }
      ],
      geo: {
        map: "hexi",
        zoom: 1.25,
        label: {
          emphasis: {
            show: false
          }
        },
        roam: false, //是否允许缩放
        itemStyle: {
          normal: {
            color: "transparent", //地图背景色
            borderColor: "transparent", //省市边界线00fcff 516a89
            borderWidth: 1
          },
          emphasis: {
            color: "transparent" //悬浮背景
          }
        }
      },
      series: [
        {
          type: "effectScatter",
          symbol: "circle",
          symbolSize: 15,
          coordinateSystem: "geo",
          z: 4,
          rippleEffect: {
            color: "red",
            period: 4,
            scale: 10,
            brushType: "stroke"
          },
          showEffectOn: "emphasis",
          label: {
            normal: {
              show: true,
              position: "bottom",
              lineHeight: 15,
              fontSize: 24,
              formatter: (b, index) => {
                let num = Math.ceil(b.name.length / 4);
                let remain = b.name.length % 4;
                let arr = [];
                for (let i = 0; i < num; i++) {
                  arr.push(b.name.substring(i * 4, (i + 1) * 4));
                }
                if (num > 2 && remain != 0) {
                  arr.push(b.name.substring(num * 4, num * 4 + remain));
                }
                return arr.length > 4
                  ? arr.splice(0, 4).join("\n")
                  : arr.join("\n");
              },
              color: "#A0D9DB",
              padding: [10, 20],
              borderColor: "#179E3F",
              backgroundColor: "rgba(0,0,0,0.6)",
              borderWidth: 1
            },
            emphasis: {}
          },

          itemStyle: {
            normal: {
              color: "#00acea",
              borderColor: "transparent"
            }
          },
          data: (this as any).dataCenter
        },
        {
          type: "effectScatter",
          symbol: "circle",
          coordinateSystem: "geo",
          symbolSize: 12,
          z: 2,
          geoIndex: 0,
          rippleEffect: {
            color: "red",
            period: 4,
            scale: 10,
            brushType: "stroke"
          },
          showEffectOn: "gender",
          label: {
            show: false,
            normal: {
              position: "right",
              textStyle: {
                fontSize: 11,
                color: "#fff"
              }
            }
          },
          itemStyle: {
            normal: {
              color: {
                type: "radial",
                x: 0.5,
                y: 0.5,
                r: 0.5,
                colorStops: [
                  {
                    offset: 0,
                    color: "rgba(255,255,255,1)"
                  },
                  {
                    offset: 0.2,
                    color: "rgba(255,255,255,1)"
                  },
                  {
                    offset: 1,
                    color: "rgba(255,255,255,0)"
                  }
                ]
              },

              opacity: 1
            }
          },
          data: (this as any).spots
        },
        {
          type: "effectScatter",
          symbol: "circle",
          coordinateSystem: "geo",
          z: 3,
          symbolSize: 6,
          geoIndex: 0,
          rippleEffect: {
            color: "red",
            period: 4,
            scale: 10,
            brushType: "stroke"
          },
          showEffectOn: "emphasis",
          label: {
            show: false,
            normal: {
              position: "right",
              textStyle: {
                fontSize: 11,
                color: "#fff"
              }
            }
          },
          itemStyle: {
            normal: {
              color: {
                type: "radial",
                x: 0.5,
                y: 0.5,
                r: 0.5,
                colorStops: [
                  {
                    offset: 0,
                    color: "rgba(0,152,247,1)"
                  },
                  {
                    offset: 0.5,
                    color: "rgba(0,152,247,1)"
                  },
                  {
                    offset: 1,
                    color: "rgba(0,152,247,0)"
                  }
                ]
              },

              opacity: 1
            }
          },
          data: (this as any).spotsScatter
        },
        {
          type: "effectScatter",
          symbol: "circle",
          coordinateSystem: "geo",
          z: 3,
          symbolSize: 6,
          geoIndex: 0,
          rippleEffect: {
            color: "red",
            period: 4,
            scale: 10,
            brushType: "stroke"
          },
          showEffectOn: "emphasis",
          label: {
            show: false,
            normal: {
              position: "right",
              textStyle: {
                fontSize: 11,
                color: "#fff"
              }
            }
          },
          itemStyle: {
            normal: {
              color: {
                type: "radial",
                x: 0.5,
                y: 0.5,
                r: 0.5,
                colorStops: [
                  {
                    offset: 0,
                    color: "rgba(0,152,247,1)"
                  },
                  {
                    offset: 0.5,
                    color: "rgba(0,152,247,1)"
                  },
                  {
                    offset: 1,
                    color: "rgba(0,152,247,0)"
                  }
                ]
              },

              opacity: 1
            }
          },
          data: (this as any).spotsLighter
        },
        {
          name: "A",
          type: "lines",
          coordinateSystem: "geo",
          z: 2,
          effect: {
            show: true,
            smooth: false,
            trailLength: 0,
            symbol: "image://red.png",
            color: "rgba(55,155,255,0.5)",
            symbolSize: [10, 20],
            symbolRotate: 90,
            period: 4
          },
          lineStyle: {
            width: 1,
            color: "rgba(255,255,255,0.7)",
            shadowColor: "rgba(1,248,243,0.7)",
            shadowBlur: 1
            //								curveness: curveness
          },
          data: (this as any).dataLinesAbnormal
        },
        {
          name: "B",
          type: "lines",
          coordinateSystem: "geo",
          z: 2,
          effect: {
            show: true,
            smooth: false,
            trailLength: 0,
            symbol: "image://green.png",
            color: "rgba(55,155,255,0.5)",
            symbolSize: [10, 20],
            symbolRotate: 90,
            period: 4
          },
          lineStyle: {
            width: 1,
            color: "rgba(255,255,255,0.7)",
            shadowColor: "rgba(1,248,243,0.7)",
            shadowBlur: 1
            //								curveness: curveness
          },
          data: (this as any).dataLinesNormal
        }
      ]
    };

    (this as any).myChart.setOption((this as any).option, false, false);
    (this as any).myChart.off("click");
    (this as any).myChart.on("click", params => {
      console.error("...", params);
    });
    (this as any).myChart.on("mouseover", params => {});
    (this as any).myChart.on("mouseout", params => {});
  }
  mounted() {
    this.registerMap();
  }
}
