var common = {
	isPhone(phone) {
		var pattern = /^[1][3,4,5,7,8][0-9]{9}$/;
		if (!pattern.test(phone) || phone == '') {
			return false;
		}
		return true;
	},
	isChinese(username) {
		var han = /^[\u4e00-\u9fa5]+$/;
		if (username == '' || !han.test(username)) {
			return false;
		};
		return true;
	},
	isEmial(email) {
		var reg = /^([a-zA-Z]|[0-9])(\w|\-)+@[a-zA-Z0-9]+\.([a-zA-Z]{2,4})$/;
		if (!reg.test(email)) {
			return false;
		}
		return true;
	},
	isMac(mac) {
		var reg = /[a-f0-9]{12}/;
		if (!reg.test(mac)) {
			return false;
		}
		return true;
	},
	isIp(ip) {
		var reg = /^((\\d|[1-9]\\d|1\\d\\d|2[0-4]\\d|25[0-5]|[*])\\.){3}(\\d|[1-9]\\d|1\\d\\d|2[0-4]\\d|25[0-5]|[*])$/;
		if (!reg.test(ip)) {
			return false;
		}
		return true;
	},
	getCurrentDate(format) {
		var now = new Date();
		var week = new Array("日", "一", "二", "三", "四", "五", "六");
		var year = now.getFullYear(); //得到年份
		var month = now.getMonth(); //得到月份
		var date = now.getDate(); //得到日期
		var day = now.getDay(); //得到周几
		var hour = now.getHours(); //得到小时
		var minu = now.getMinutes(); //得到分钟
		var sec = now.getSeconds(); //得到秒
		month = month + 1;
		if (month < 10) month = "0" + month;
		if (date < 10) date = "0" + date;
		if (hour < 10) hour = "0" + hour;
		if (minu < 10) minu = "0" + minu;
		if (sec < 10) sec = "0" + sec;
		var time = "";
		//精确到天
		if (format == 1) {
			time = year + "年" + month + "月" + date+ "日";
		}
		//精确到分
		else if (format == 2) {
			time = hour + ":" + minu + ":" + sec + "\xa0\xa0" + "星期" + week[day] + "\xa0\xa0\xa0\xa0" + year + "年" + month + "月" + date + "日";
		}
		return time;
	},
	getDateArray(n) {
		var array = [];
		var now = new Date();
		var year = (now.getFullYear()+'').substring(2,4);
		var month = now.getMonth()+1;
		for (let i = month; i > 1; i--) {
			if(array.length<n){
				array.push(year + '-' + i)
			}
		}
		let previous = (parseInt(year)- 1)<10?'0'+(parseInt(year)- 1):(parseInt(year)- 1);
		for (let i = 0; i <= n - array.length; i++) {
			array.push( previous+ '-' + (12 - i))
		}
		return array;
	}
}

export default common;