



import echarts from "echarts";
import { Component, Prop, Vue } from "vue-property-decorator";

@Component
export default class Pie extends Vue {
  myChart: any = null;
  option: any = null;
  data: any = null;
  initData(data) {
    this.data = data;
    this.initPie(data.data);
  }
  listenScreen() {
    window.addEventListener("resize", () => {
      this.myChart.resize();
    });
  }
  initPie(data: Array<any>) {
    this.listenScreen();
    this.myChart = this.$echarts.init(this.$el);
    this.option = {
      legend: {
        show: false,
        width: "90%",
        left: "center",
        itemWidth: 12,
        itemHeight: 12,
        textStyle: {
          color: "#fff",
          fontSize: 12
        },
        icon: "rect",
        right: "0",
        bottom: 5,
        itemGap: 10,
        padding: [5, 15]
      },
      series: [
        {
          type: "pie",
          radius: ["60%", "90%"],
          center: ["50%", "50%"],
          avoidLabelOverlap: false,
          color: this.data.color,
          itemStyle: {
            normal: {
              borderWidth: 5,
              borderColor: "#001b31"
            }
          },
          // hoverAnimation:false,
          label: {
            normal: {
              show: false,
              position: "center",
              formatter: function(data) {
                // 设置圆饼图中间文字排版
                return data.value+'%' + "\n" + data.name;
              }
            },
            emphasis: {
              show: true, //文字至于中间时，这里需为true
              textStyle: {
                //设置文字样式
                fontSize: "18",
                lineHeight:30,
                color: "#fff"
              }
            }
          },
          data: data
        },
        {
          type: "pie",
          radius: ["49%", "50%"],
          center: ["50%", "50%"],
          data: [100],
          color: "#05e7e4",
          label: {
            show: false
          }
        }
      ]
    };
    this.myChart.setOption(this.option, true);
    var charPiecurrentIndex = 0;
    setInterval(() => {
      var dataLen = this.option.series[0].data.length;
      // 取消之前高亮的图形
      this.myChart.dispatchAction({
        type: "downplay",
        seriesIndex: 0,
        dataIndex: charPiecurrentIndex
      });
      charPiecurrentIndex = (charPiecurrentIndex + 1) % dataLen;
      this.myChart.dispatchAction({
        type: "highlight",
        seriesIndex: 0,
        dataIndex: charPiecurrentIndex
      });
    }, 2000);
  }
  setInterval() {}
  mounted() {
    // this.initPie();
  }
}
