<template>
  <div>
    <div class="topoLines" ref="topoLines"></div>
  </div>
</template>
<script>
export default {
  name: "topoLines",
  data() {
    return {
      fontSizeOne: 0.32 * this.$rem.rem(),
      fontSizeTwo: 0.2 * this.$rem.rem(),
      fontSizeThree: 0.4 * this.$rem.rem(),
      fontSizeFour: 0.45 * this.$rem.rem(),
      fontSizeFive: 0.7 * this.$rem.rem(),
      myChart: "",
      option: "",
      //曲线向左弯曲
      //				namesOne:["渝西监狱","永川监狱","九龙监狱","长康监狱","女子监狱"],
      //曲线向右弯曲
      namesTwo: [
        "重庆市第一看守所",
        "渝都监狱",
        "大坪监狱",
        "重庆市检察院",
        "检察院第五分院"
      ],
      /*level为0表示监控,展示白色.level为1表示有异常,展示橙色.*/
      items: [
        {
          level: 0,
          name: "渝西监狱",
          value: [17, 47]
        },
        {
          level: 1,
          name: "永川监狱",
          value: [22.5, 31.5]
        },
        {
          level: 0,
          name: "九龙监狱",
          value: [47, 42]
        },
        {
          level: 1,
          name: "长康监狱",
          value: [46, 24]
        },
        {
          level: 0,
          name: "女子监狱",
          value: [52, 37]
        },
        {
          level: 1,
          name: "重庆市第一看守所",
          value: [56, 47]
        },
        {
          level: 0,
          name: "渝都监狱",
          value: [74, 42]
        },
        {
          level: 1,
          name: "大坪监狱",
          value: [66, 47]
        },
        {
          level: 0,
          name: "重庆市检察院",
          value: [70, 67]
        },
        {
          level: 1,
          name: "检察院第五分院",
          value: [55, 60]
        }
      ],
      //终点的颜色。第一个是蓝色,第二个是橙色
      colorList: [
        {
          color: {
            type: "radial",
            x: 0.5,
            y: 0.5,
            r: 0.5,
            colorStops: [
              {
                offset: 0,
                color: "white" // 0% 处的颜色
              },
              {
                offset: 0.8,
                color: "white" // 40% 处的颜色
              },
              {
                offset: 0.81,
                color: "rgba(34,223,251,1)" // 0% 处的颜色
              },
              {
                offset: 1,
                color: "rgba(34,223,251,1)" // 0% 处的颜色
              }
            ],
            global: false // 缺省为 false
          }
        },
        {
          color: {
            type: "radial",
            x: 0.5,
            y: 0.5,
            r: 0.5,
            colorStops: [
              {
                offset: 0,
                color: "white" // 0% 处的颜色
              },
              {
                offset: 0.8,
                color: "white" // 0% 处的颜色
              },
              {
                offset: 0.81,
                color: "red" // 0% 处的颜色
              },
              {
                offset: 1,
                color: "red" // 0% 处的颜色
              }
            ],
            global: false // 缺省为 false
          }
        }
      ],
      //飞线的颜色。第一条是白色,第二条是橙色
      lineColorList: [
        {
          lineColor: {
            color: "white",
            width: 3,
            curveness: NaN
          }
        },
        {
          lineColor: {
            color: "#FE6802",
            width: 3,
            curveness: NaN
          }
        }
      ]
    };
  },
  mounted() {
    // this.$echarts.registerMap("JL", require("../assets/json/jilin.json"));
    // this.init();
    // this.initItems();
    // this.color();
  },
  methods: {
    init() {
      window.addEventListener("resize", () => {
        this.myChart.resize();
      });
    },
    initItems() {
      console.error("topoLines地图组件收到的数据为-", this.linkStates);
      this.items.forEach((item, index) => {
        item.level = this.linkStates.find(link => {
          return item.name == link.name;
        }).level;
      });
    },
    color(itemsParent) {
      console.error("topoLines地图组件收到的数据为-", itemsParent);
      this.items.forEach((item, index) => {
        item.level = itemsParent.find(link => {
          return item.name == link.name;
        }).level;
      });
      console.error("接收level以后的items---", this.items);
      this.items.forEach((item, index) => {
        item.symbol = "image://地标.png";
        item.itemStyle = this.colorList[item.level];
        var lineColor = Object.assign(
          {},
          this.lineColorList[item.level].lineColor
        );
        item.lineColor = lineColor;
      });
      this.drawLines();
    },
    drawLines() {
      var planePath =
        "path://M1705.06,1318.313v-89.254l-319.9-221.799l0.073-208.063c0.521-84.662-26.629-121.796-63.961-121.491c-37.332-0.305-64.482,36.829-63.961,121.491l0.073,208.063l-319.9,221.799v89.254l330.343-157.288l12.238,241.308l-134.449,92.931l0.531,42.034l175.125-42.917l175.125,42.917l0.531-42.034l-134.449-92.931l12.238-241.308L1705.06,1318.313z";
      const targetCoord = [55, 60];
      //				const curveness = -0.3;
      var linesData1 = [];
      var linesData2 = [];
      var linesData3 = [];
      var lineColor = [];
      var categories = [
        {
          itemStyle: {},
          label: {}
        }
      ];
      var item = {
        name: "",
        value: targetCoord,
        //起点大小
        symbolSize: [0, 0]
      };
      var data = this.items.concat([item]);
      this.items.forEach(el => {
        let flag = this.namesTwo.some(item => {
          return item == el.name;
        });
        el.lineColor.curveness = flag ? 0.3 : -0.3;
        if (el.level == 0) {
          linesData1.push([
            {
              coord: targetCoord,
              lineStyle: el.lineColor
            },
            {
              coord: el.value
            }
          ]);
        } else if (el.level == 1) {
          linesData2.push([
            {
              coord: targetCoord,
              lineStyle: el.lineColor
            },
            {
              coord: el.value
            }
          ]);
        }
      });
      console.error(111, this.items);
      this.myChart = this.$echarts.init(this.$refs.topoLines);
      this.option = {
        legend: [],
        tooltip: {
          trigger: "item",
          formatter: (params, ticket, callback) => {
            return params.name;
          }
        },
        grid: {
          top: "0%",
          left: "0%",
          height: "100%",
          width: "100%"
        },
        xAxis: {
          splitLine: {
            show: false
          },
          axisLine: {
            show: false
          },
          axisTick: {
            show: false
          },
          axisLabel: {
            show: false
          },
          max: 100,
          min: 0
        },
        yAxis: {
          splitLine: {
            show: false
          },
          axisLine: {
            show: false
          },
          axisTick: {
            show: false
          },
          axisLabel: {
            show: false
          },
          max: 100,
          min: 0
        },
        geo: {
          map: "JL",
          // zlevel: 200,
          label: {
            normal: {
              show: true,
              color: "black"
            },
            emphasis: {
              show: true,
              color: "red"
            }
          },
          aspectScale: 0.75,
          roam: true,
          top: "10%",
          //地图缩放比例
          scaleLimit: {
            min: 1.2,
            max: 3
          },
          itemStyle: {
            normal: {
              areaColor: "transparent",
              borderColor: "red",
              // borderColor: "transparent",
              borderWidth: 1,
              shadowColor: "transparent",
              shadowBlur: 10,
              shadowOffsetX: 10,
              shadowOffsetY: 10
            },
            emphasis: {
              areaColor: "rgba(107,184,222,0.5)"
              // areaColor: "rgba(255,242,0,0.5)"
            }
          }
        },
        series: [
          {
            type: "graph",
            layout: "none",
            coordinateSystem: "cartesian2d",
            // symbol:"image://white.png",
            //终点大小
            symbolSize: [40, 40],
            z: 3,
            circular: {
              rotateLabel: true
            },
            //名称标签样式
            label: {
              normal: {
                show: true,
                position: "bottom",
                distance: 5,
                backgroundColor: "red",
                borderColor: "blue",
                borderWidth: 1,
                borderRadius: 10,
                padding: [10, 30, 10, 30],
                offset: [0, 0],
                color: "#fff",
                textBorderColor: "#6bb8de",
                textBorderWidth: 1.5,
                fontSize: this.fontSizeOne
              },
              emphasis: {
                show: false
              }
            },
            itemStyle: {
              normal: {
                shadowColor: "none"
              }
            },
            data: data,
            categories: categories
          },
          {
            name: "A",
            type: "lines",
            coordinateSystem: "cartesian2d",
            z: 1,
            effect: {
              show: true,
              smooth: false,
              trailLength: 0,
              symbol: "image://white.png",
              color: "rgba(55,155,255,0.5)",
              symbolSize: [this.fontSizeTwo, this.fontSizeThree],
              symbolRotate: 90,
              period: 4
            },
            lineStyle: {
              width: 2,
              color: "rgba(255,255,255,0.7)",
              shadowColor: "rgba(1,248,243,0.7)",
              shadowBlur: 1
              //								curveness: curveness
            },
            data: linesData1
          },
          {
            name: "B",
            type: "lines",
            coordinateSystem: "cartesian2d",
            z: 1,
            effect: {
              show: true,
              smooth: false,
              trailLength: 0,
              symbol: "image://orange.png",
              color: "rgba(55,155,255,0.5)",
              //移动光点大小
              symbolSize: [this.fontSizeTwo, this.fontSizeThree],
              symbolRotate: 90,
              period: 4
            },
            lineStyle: {
              width: 2,
              color: "rgba(255,255,255,0.7)",
              shadowColor: "rgba(1,248,243,0.7)",
              shadowBlur: 1
              //								curveness: curveness
            },
            data: linesData2
          }
        ]
      };
      this.myChart.setOption(this.option);
    }
  }
};
</script>
<style lang="scss" scoped>
.topoLines {
  position: absolute;
  width: 100%;
  height: 100%;
  // background:url(../assets/img/吉林地图.png) center center no-repeat;
  // background-size: 50%;
}
</style>