




import echarts from "echarts";
import { Component, Prop, Vue } from "vue-property-decorator";

@Component
export default class BarFour extends Vue {
  myChart: any = null;
  option: any = null;
  value: Array<any> = [10,20,30,40,70];
  name: Array<any> = ["设备1", "设备2", "设备3", "设备4", "设备5"];

  fontSizeOne: number = 0.28 * (this as any).$rem.rem();
  mounted() {}
  listenScreen() {
    window.addEventListener("resize", () => {
      this.myChart.resize();
    });
  }
  initData(data: Array<any>) {
    if (data.length != 0) {
      this.name = data.map((item: any) => {
        return item.name;
      });
      this.value = data.map((item: any) => {
        return item.value;
      });
    }
    this.initBar();
  }
  initBar() {
    this.listenScreen();
    this.myChart = this.$echarts.init(this.$el);

    this.option = {
      grid: {
        show: false,
        left: "15%",
        top: "0%",
        bottom: "0",
        right: "20%"
      },
      backgroundColor: "transparent", //背景色
      xAxis: {
        show: false, //是否显示x轴
        type: "value"
      },
      yAxis: {
        type: "category",
        inverse: false, //让y轴数据逆向
        axisLabel: {
          show: true,
          textStyle: {
            color: "#fff" //y轴字体颜色
          },
          formatter: (c, index) => {
            let num = Math.ceil(c.length / 5);
            let remain = c.length % 5;
            let arr = [];
            for (let i = 0; i < num; i++) {
              arr.push(c.substring(i * 5, (i + 1) * 5));
            }
            if (num > 1 && remain != 0) {
              arr.push(c.substring(num * 5, num * 5 + remain));
            }
            return arr.length > 4
              ? arr.splice(0, 4).join("\n")
              : arr.join("\n");
          },
          //定义富文本标签
          rich: {
            lg: {
              fontWeight: "bold",
              fontSize: 12, //字体默认12
              color: "#08C",
              padding: [0, 5, 0, 0]
            },
            title: {
              color: "#fff",
              fontWeight: "lighter"
            }
          }
        },
        splitLine: { show: false }, //横向的线
        axisTick: { show: false }, //y轴的端点
        axisLine: { show: false }, //y轴的线
        data: this.name
      },
      series: [
        {
          name: "数据内框",
          type: "bar",
          itemStyle: {
            normal: {
              barBorderRadius: 30,
              //4个参数用于配置渐变色的起止位置, 这4个参数依次对应右/下/左/上,0, 0, 0, 1表示渐变色从正上方开始
              color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [
                {
                  offset: 0,
                  color: "rgba(45,153,218,1)"
                },
                {
                  offset: 0.4,
                  color: "rgba(45,153,218,0.5)"
                },
                {
                  offset: 0.6,
                  color: "rgba(45,153,218,0.5)"
                },
                {
                  offset: 1,
                  color: "rgba(45,153,218,1)"
                }
              ])
              // color:{
              //     image:'image://blueBar.png',
              //     repeat:'no-repeat'
              // }
            }
          },
          shadowColor: "#34A6E6",
          shadowBlur: 0,
          label: {
            normal: {
              show: false,
              position: "top",
              color: "red",
              fontSize: 14,
              formatter: param => {}
            }
          },
          barWidth: 30,
          data: this.value
        },
        {
          name: "外框",
          type: "bar",
          itemStyle: {
            normal: {
              barBorderRadius: 30,
              color: "rgba(255, 255, 255, 0.14)" //rgba设置透明度0.14
            }
          },
          barGap: "-100%",
          z: 0,
          barWidth: 30,
          data: [100, 100, 100, 100],
          label: {
            normal: {
              show: true,
              position: "right",
              color: "rgb(5, 200, 249)",
              fontSize: 14,
              formatter: param => {
                console.error("cpu---param---", param);
                return this.value[param.dataIndex] + "%";
              }
            }
          }
        }
      ]
    };
    this.myChart.setOption(this.option, true);
  }
}
