




import echarts from "echarts";
import { Component, Prop, Vue } from "vue-property-decorator";

@Component
export default class Bar extends Vue {
  beforeCreate() {
    this["myChart"] = null;
    this["option"] = null;
    this["series"] = [];
    this["timeData"] = [];
    this["data"] = [];
    this["legendData"] = [];
    this["colorData"] = [];
    this["showLegend"] = false;
    this["name"] = "";
    this["max"] = 0;
    this["showAreaColor"] = false;
    this["piecesColor"] = [
      "#FFFFFF",
      "#00EBFF",
      "#06FC4E",
      "#FDF006",
      "#FF7F27",
      "#FF0000"
    ];
    this["pieces"] = [];
  }
  mounted() {}
  listenScreen() {
    window.addEventListener("resize", () => {
      (this as any).myChart.resize();
    });
  }
  initData(data: any) {
    (this as any).legendData = data.legendData;
    (this as any).timeData = data.timeData;
    (this as any).data = data.data;
    (this as any).colorData = data.colorData;
    (this as any).name = data.name;
    (this as any).showLegend = data.showLegend;
    (this as any).showAreaColor = data.showAreaColor;
    // let temp = this.data.flat();
    let temp = [];
    for (let i = 0; i < (this as any).data.length; i++) {
      temp = temp.concat((this as any).data[i]);
    }
    (this as any).max = Math.max(...temp);
    this.generatePieces((this as any).max);
    this.generateSeries();
    this.initBar();
  }
  generatePieces(max: number) {
    //根据最大值来动态计算不同颜色的数据区间
    // let num = Math.floor(max / 6);
    let num = 500;
    (this as any).pieces = (this as any).piecesColor.map((item, index) => {
      return {
        gte: index * num,
        lte: (index + 1) * num,
        color: item,
        colorAlpha: 1
      };
    });
    console.error("pieces-----", (this as any).pieces);
  }
  generateSeries() {
    (this as any).series = (this as any).data.map(item => {
      return {
        data: item.map(item2 => {
          //根据数据大小来动态计算symbolSize大小
          let temp = 0;
          (this as any).pieces.forEach((item3, index3) => {
            if (item2 >= item3.gte && item2 < item3.lte) {
              temp = index3 <= 2 ? 15 : (index3 + 1) * 5;
            }
          });
          return {
            value: item2,
            symbolSize: temp,
            itemStyle: { opacity: 1 }
          };
        }),
        type: "line",
        symbol: "circle",
        itemStyle: {
          borderWidth: 2
        }
      };
    });
    (this as any).series.push({
      name: "每日跑步里程",
      type: "line",
      smooth: true,
      symbol: "none",
      symbolSize: 0,
      itemStyle: {
        normal: {
          color: "#fff"
        }
      },
      lineStyle: {
        normal: {
          color: "transparent",
          width: 3
        }
      },

      data: [2000, 2000, 2000, 2000, 2000],
      lineSmooth: true,
      markLine: {
        silent: true,
        symbol: "none",
        data: [
          {
            type: "average",
            name: "平均值"
          }
        ],
        precision: 0,
        label: {
          normal: {
            formatter: ""
          }
        },
        lineStyle: {
          normal: {
            color: "red"
          }
        }
      },
      tooltip: {
        position: "top",
        formatter: "{c} m",
        backgroundColor: "rgba(28,152,232,.2)",
        padding: 6
      }
    });
  }
  initBar() {
    this.listenScreen();
    (this as any).myChart = this.$echarts.init(this.$el);

    (this as any).option = {
      title: {
        show: false,
        text: "",
        top: "0%",
        left: "center",
        textStyle: {
          color: "#FFF",
          align: "center"
        }
      },
      backgroundColor: "transparent",
      grid: {
        left: "5%",
        right: "5%",
        top: "15%",
        bottom: "0%",
        containLabel: true
      },
      tooltip: {
        show: true,
        trigger: "item",
        formatter: "{c}"
      },
      lineStyle: {
        color: "transparent"
      },
      legend: {
        show: (this as any).showLegend,
        x: "right",
        top: "0%",
        y: "35",
        icon: "stack",
        itemWidth: 10,
        itemHeight: 10,
        textStyle: {
          color: "#1bb4f6"
        },
        data: (this as any).legendData
      },
      xAxis: [
        {
          type: "category",
          boundaryGap: true,
          gridIndex: 0,
          axisLabel: {
            color: "#2ad1d2",
            margin: 16,
            align: "center",
            interval: 0,
            padding: [0, 0, 0, 0],
            lineHeight: 15,
            formatter: (c, index) => {
              let num = Math.ceil(c.length / 4);
              let remain = c.length % 4;
              let arr = [];
              for (let i = 0; i < num; i++) {
                arr.push(c.substring(i * 4, (i + 1) * 4));
              }
              if (num > 1 && remain != 0) {
                arr.push(c.substring(num * 4, num * 4 + remain));
              }
              return arr.length > 4
                ? arr.splice(0, 4).join("\n")
                : arr.join("\n");
            }
          },
          axisLine: {
            show: true,
            lineStyle: {
              color: "#27b4c2"
            }
          },
          axisTick: {
            show: false
          },
          splitLine: {
            show: false,
            lineStyle: {
              color: "#195384"
            }
          },
          data: (this as any).timeData
        }
      ],
      yAxis: [
        {
          type: "value",
          name: (this as any).name,
          nameTextStyle: {
            fontSize: 16,
            // fontWeight:'bold',
            align: "left",
            width: 300,
            padding: [0, 0, 0, 40],
            color: "#0BC3DB"
          },
          nameGap: 15,
          min: 0,
          max: 3000,
          axisLabel: {
            formatter: (value, index) => {
              return value;
            },
            margin: 12,
            textStyle: {
              color: "#2ad1d2"
            }
          },
          axisLine: {
            lineStyle: {
              color: "#27b4c2"
            }
          },
          axisTick: {
            show: false
          },
          splitLine: {
            show: false,
            lineStyle: {
              color: "#11366e"
            }
          }
        }
      ],
      series: (this as any).series,
      visualMap: [
        {
          show: false,
          pieces: (this as any).pieces,
          outOfRange: {
            color: "#FA02FE",
            symbolSize: 8,
            colorAlpha: 0.6
          }
        }
      ]
    };
    (this as any).myChart.setOption((this as any).option, true);
    (this as any).myChart = null;
    (this as any).option = null;
  }
}
