























import { Component, Prop, Vue } from "vue-property-decorator";
@Component
export default class Table extends Vue {
  data: any = {};
  classOption: any = {
    step: 1, //步长
    limitMoveNum: 10, //启动无缝滚动最小数据数
    hoverStop: true, //是否启用鼠标hover控制
    direction: 1, //1 往上 0 往下
    openWatch: false, //开启data实时监听
    singleHeight: 0, //单条数据高度有值hoverStop关闭
    // singleHeight: 40,
    waitTime: 5000 //单步停止等待时间
  };
  getWidth(attr) {
		let w = '5%'
		this.data.columns.forEach(item =>{
			if(item.prop == attr){
				w = item.width
			}
		})
    return 'width:'+ w +';'
  }
  initData(data) {
    this.data = data;
  }
}
