



import echarts from "echarts";
import { Component, Prop, Vue } from "vue-property-decorator";

@Component
export default class Pie extends Vue {
  myChart: any = null;
  option: any = null;
  data: any = null;
  initData(data) {
    this.data = data;
    this.initPie(data);
  }
  listenScreen() {
    window.addEventListener("resize", () => {
      this.myChart.resize();
    });
  }
  initPie(data) {
    this.listenScreen();
    this.myChart = this.$echarts.init(this.$el);
    this.option = {
      title: {
        x: "48%",
        textAlign: "center",
        top: "65%", //字体的位置
        text: data.value,
        subtext: "综合评分",
        textStyle: {
          fontWeight: "normal",
          color: "#fefeff",
          fontSize: 22,
          textShadowColor: "#0061a9",
          textShadowBlur: 20
        },
        subtextStyle: {
          fontSize: 18,
          color: "#cdccca",
          verticalAlign: "bottom"
        }
      },
      series: [
        {
          // 主圆环
          name: data.name,
          type: "pie",
          radius: ["55%", "70%"],
          startAngle: 225,
          color: [
            {
              type: "linear",
              x: 1,
              y: 0,
              x2: 0,
              y2: 0,
              colorStops: [
                {
                  offset: 0,
                  color: "#00f4ff" // 0% 处的颜色
                },
                {
                  offset: 1,
                  color: "#0f83d0" // 100% 处的颜色
                }
              ]
            },
            "transparent"
          ],
          hoverAnimation: true,
          legendHoverLink: false,
          z: 10,
          labelLine: {
            normal: {
              show: false
            }
          },
          data: [
            {
              value: (75 * data.value) / 100
            },
            {
              value: 100 - (75 * data.value) / 100
            }
          ]
        },
        {
          // 背景圆环
          name: "",
          type: "pie",
          radius: ["55%", "70%"],
          silent: true,
          startAngle: 225,
          labelLine: {
            normal: {
              show: false
            }
          },
          z: 5,
          data: [
            {
              value: 75,
              itemStyle: {
                color: "#30415b"
              }
            },
            {
              value: 25,
              itemStyle: {
                color: "transparent"
              }
            }
          ]
        },
        {
          // 内圆环
          name: "",
          type: "pie",
          radius: ["48%", "50%"],
          silent: true,
          startAngle: 225,
          labelLine: {
            normal: {
              show: false
            }
          },
          z: 5,
          data: [
            {
              value: 75,
              itemStyle: {
                color: "#087fb9"
              }
            },
            {
              value: 25,
              itemStyle: {
                color: "transparent"
              }
            }
          ]
        },
        {
          // 背景
          name: "",
          type: "pie",
          cursor: "default",
          radius: ["0", "75%"],
          hoverAnimation: false,
          legendHoverLink: false,
          itemStyle: {
            color: "#001926",
            shadowColor: "#023e63",
            shadowBlur: 10
          },
          labelLine: {
            normal: {
              show: false
            }
          },
          data: [
            {
              value: 1,
              label: {
                normal: {
                  formatter: data.level,
                  position: "center",
                  show: true,
                  textStyle: {
                    fontSize: "20",
                    fontWeight: "normal",
                    color: "#fefeff",
                    textShadowColor: "#0061a9",
                    textShadowBlur: 20
                  }
                }
              }
            }
          ]
        }
      ]
    };
    this.myChart.setOption(this.option, true);
  }
  mounted() {
    // this.initPie();
  }
}
