




import echarts from "echarts";
import { Component, Prop, Vue } from "vue-property-decorator";

@Component
export default class myBar extends Vue {
  beforeCreate() {
    this["myChart"] = null;
    this["option"] = null;
    this['myBlue'] = new echarts.graphic.LinearGradient(0, 0, 0, 1, [
    {
      offset: 1,
      color: "rgba(0,127,217,0)"
    },
    {
      offset: 0,
      color: "rgba(0,127,217,0.6)"
    }
  ]);
  this['myGray'] = new echarts.graphic.LinearGradient(0, 0, 0, 1, [
    {
      offset: 1,
      color: "rgba(175,179,183,0)"
    },
    {
      offset: 0,
      color: "rgba(175,179,183,1)"
    }
  ]);
  this['series'] = [];
  this['timeData'] = [];
  this['data'] = [];
  this['legendData'] = [];
  this['colorData'] = [];
  this['showLegend'] = false;
  this['name'] = "";
  this['max'] = 0;
  }
  mounted() {
    var data: Array<any> = [];
  }
  listenScreen() {
    window.addEventListener("resize", () => {
      (this as any).myChart.resize();
    });
  }
  initData(data: any) {
    (this as any).max = 0;
    (this as any).timeData = data.timeData;
    (this as any).data = data.data;
    // let temp = this.data.flat();
    // this.max = Math.max(...temp);
    (this as any).data.forEach((item, index) => {
      (this as any).max += Math.max(...item);
    });
    (this as any).legendData = data.legendData;
    (this as any).showLegend = data.showLegend;
    (this as any).name = data.name;
    this.initBar();
  }
  initBar() {
    this.listenScreen();
    (this as any).myChart = this.$echarts.init(this.$el);

    (this as any).option = {
      backgroundColor: "transparent",
      color: ["rgba(0,127,217,1)", "rgba(175,179,183,1)"],
      grid: {
        left: "5%",
        right: "5%",
        top: "10%",
        bottom: "5%",
        containLabel: true
      },
      legend: {
        show: (this as any).showLegend,
        top: "0%",
        right: "5%",
        icon: "stack",
        itemWidth: 10,
        itemHeight: 10,
        textStyle: {
          color: "#1bb4f6"
        },
        data: (this as any).legendData
      },
      tooltip: {
        trigger: "axis",
        backgroundColor: "rgba(3, 3, 3, .4)",
        padding: [0, 0, 0, 0],
        textStyle: {
          color: "white"
        },
        axisPointer: {
          type: "line",
          animation: true,
          lineStyle: {
            color: "transparent"
          }
        },
        extraCssText: "box-shadow: 0px 0px 10px -4px rgba(3, 3, 3, .4)",
        formatter: (params, ticket, callback) => {
          let htmls = "<div>",
            xaxisName = "";
          if (params.length > 0) {
            xaxisName = params[0].axisValue;
            // htmls +=
            //   '<div style="font-size:16px;height:32px;color:#0069FF;border-radius:4px;line-height:36px;padding-left:15px;text-align: left;">' +
            //   xaxisName +
            //   "</div><div>";
            for (let j = 0; j < params.length; j++) {
              htmls +=
                '<p style="font-size:14px;padding:4px 23px 6px 15px;color:white;text-align: left;">' +
                params[j].seriesName +
                " : " +
                params[j].data +
                "</p>";
            }
            htmls += "</div>";
            return htmls;
          }
        }
      },
      xAxis: [
        {
          type: "category",
          boundaryGap: true,
          axisLabel: {
            color: "#2ad1d2",
            align: "center"
          },
          axisLine: {
            show: true,
            lineStyle: {
              color: "#27b4c2"
            }
          },
          axisTick: {
            show: false
          },
          splitLine: {
            show: false,
            lineStyle: {
              color: "#195384"
            }
          },
          data: (this as any).timeData
        }
      ],
      yAxis: [
        {
          type: "value",
          name: "",
          nameGap: 15,
          nameTextStyle: {
            fontSize: 16,
            align: "left",
            width: 300,
            padding: [0, 0, 0, 50],
            color: "#0BC3DB"
          },
          min: 0,
          max: (this as any).max + 80,
          axisLabel: {
            formatter: "{value}",
            textStyle: {
              color: "#2ad1d2"
            }
          },
          axisLine: {
            lineStyle: {
              color: "#27b4c2"
            }
          },
          axisTick: {
            show: false
          },
          splitLine: {
            show: false,
            lineStyle: {
              color: "#11366e"
            }
          }
        }
      ],

      series: [
        {
          name: "未完成",
          type: "bar",
          stack: "a",
          barWidth: "30%",
          barGap: "-100%",
          data: (this as any).data[1],
          itemStyle: {
            color: (this as any).myBlue
          },
          label: {
            show: true,
            position: "top",
            color: "white",
            fontSize: 16,
            formatter: params => {
              return params.dataIndex == 11 ? params.data:'';
            }
            
          },
          z: 2
        },
        {
          name: "已完成",
          type: "bar",
          stack: "a",
          barWidth: "30%",
          barGap: "-100%",
          //   barCategoryGap:10,
          data: (this as any).data[0],
          itemStyle: {
            color: (this as any).myGray
          },
          label: {
            show: true,
            position: "top",
            color: "white",
            fontSize: 16,
            formatter: params => {
              return params.data;
            }
          },
          z: 3
        }
      ]
    };
    (this as any).myChart.setOption((this as any).option, true);
    (this as any).myChart = null;
    (this as any).option = null;
  }
}
