



import echarts from "echarts";
import { Component, Prop, Vue } from "vue-property-decorator";
import { createWriteStream } from "fs";

@Component
export default class Bar extends Vue {
  myChart: any = null;
  option: any = null;
  attr:any = {};
  initData(data) {
    this.attr = data.barAttr
    this.initPie(data.data,data.colorArray);
  }
  listenScreen() {
    window.addEventListener("resize", () => {
      this.myChart.resize();
    });
  }
  initPie(data: Array<any>,colorArray:Array<any>) {
    let name = [];
    let num = [];
    data.forEach((item, index) => {
      name.push(item.name);
      num.push(100);
    });
    this.listenScreen();
    this.myChart = this.$echarts.init(this.$el);
    this.option = {
      grid: {
        left: 10,
        bottom: 15,
        top: 15,
        containLabel: true
      },
      xAxis: {
        type: "value",
        splitLine: {
          show: false
        },
        axisLine: {
          lineStyle: {
            color: "#0daed7"
          }
        }
      },
      yAxis: {
        type: "category",
        axisLine: {
          lineStyle: {
            color: "#0daed7"
          }
        },
        axisLabel: {
          interval: 0,
          rotate: "0",
          textStyle: {
            fontSize: 12,
            color: "#7CE9FB"
          }
        },
        data: name
      },
      series: [
        {
          name: "数据内框",
          type: "bar",
          itemStyle: {
            normal: {
              barBorderRadius: this.attr.barBorderRadius,
              color: function(params) {
                return {
                  type: "linear",
                  colorStops: colorArray[params.dataIndex]
                };
              }
            }
          },
          barWidth: 11,
          data: data
        },
        {
          name: "外框",
          type: "bar",
          itemStyle: {
            normal: {
              barBorderRadius: this.attr.barBorderRadius,
              color: "rgba(255, 255, 255, 0.14)" //rgba设置透明度0.14
            }
          },
          barGap: "-100%",
          z: 0,
          barWidth: 11,
          data: num
        }
      ]
    };

    this.myChart.setOption(this.option, true);
  }
  mounted() {
    // this.initPie();
  }
}
