





import echarts from "echarts";
import "echarts-liquidfill";
import { Component, Prop, Vue } from "vue-property-decorator";

@Component
export default class Liquidfill extends Vue {
  myChart: any = null;
  option: any = null;
  data: any = null;
  initData(data) {
    this.initPie(data);
  }
  listenScreen() {
    window.addEventListener("resize", () => {
      this.myChart.resize();
    });
  }
  initPie(data: Array<any>) {
    this.listenScreen();
    this.myChart = this.$echarts.init(this.$el);
    var titleArr = [];
    var seriesData = [];
    if (data != null && data.length > 0) {
      data.forEach((item, index) => {
        titleArr.push({
            text: item.name,
            top: "85%",
            left:item.left,
            // left: index * 50 + 15 + "%",
            textStyle: {
              color: "#fff",
              fontSize: 16,
              fontWeight: "normal"
            }
          }
        );
        var liquidFill = {
          type: "liquidFill",
          radius: "60%",
          center: item.center,
          label: {
            fontSize: 26,
            color: "#fff",
            formatter: function(param) {
              let value = (param.value * 100).toFixed(0)
              return value + "%";
            }
          },
          outline: {
            show: true,
            borderDistance: 0,
            itemStyle: {
              borderWidth: 20,
              borderColor: "#020c23",
              shadowBlur: 20,
              shadowColor: item.bottomColor
            }
          },
          backgroundStyle: {
            color: "transparent"
          },
          data: [
            {
              value: item.value,
              itemStyle: {
                normal: {
                  shadowBlur: 15,
                  shadowColor: item.bottomColor,
                  color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [
                    {
                      offset: 0,
                      color: item.topColor
                    },
                    {
                      offset: 1,
                      color: item.bottomColor
                    }
                  ]),
                  opacity: 1
                }
              }
            }
          ]
        };
        var pie = {
          type: "pie",
          center: item.center,
          radius: ["58%", "63%"],
          avoidLabelOverlap: false,
          label: {
            normal: {
              show: false
            }
          },
          data: [
            {
              name: item.name,
              value: item.value,
              itemStyle: {
                normal: {
                  color: item.topColor
                }
              }
            },
            {
              name: "",
              value: 1 - item.value,
              itemStyle: {
                normal: {
                  color: item.bottomColor
                }
              }
            }
          ]
        };
        seriesData.push(liquidFill);
        seriesData.push(pie);
      });
    }
    this.option = {
      title: titleArr,
      series: seriesData
    };
     this.myChart.clear();
    this.myChart.setOption(this.option, true);
    
  }
  mounted() {
    // var container = this.$el;
    // this.myChart = echarts.init(container);
    // this.initPie();
  }
}
