




import echarts from "echarts";
import { Component, Prop, Vue } from "vue-property-decorator";

@Component
export default class Bar extends Vue {
beforeCreate() {
    this["myChart"] = null;
    this["option"] = null;

    this['series'] = [];
  this['timeData'] = [];
  this['data'] = [];
  this['legendData'] = [];
  this['colorData'] = [];
  this['showLegend'] = false;
  this['name'] = "";
  this['max'] = 0;
  this['showAreaColor'] = false;
  this['showLabel'] = true;
  }
  mounted() {
    this.listenScreen();
  }
  listenScreen() {
    window.addEventListener("resize", () => {
      (this as any).myChart.resize();
    });
  }
  initData(data: any) {
    (this as any).legendData = data.legendData;
    (this as any).timeData = data.timeData;
    (this as any).data = data.data;
    (this as any).colorData = data.colorData;
    (this as any).name = data.name;
    (this as any).showLegend = data.showLegend;
    (this as any).showAreaColor = data.showAreaColor;
    (this as any).max = 100;
    this.generateSeries();
  }
  generateSeries() {
    (this as any).series = (this as any).data.map((item, index) => {
      return {
        name: (this as any).legendData[index],
        type: "line",
        symbol: "circle",
        symbolSize: 6,
        smooth: false,
        animation: true,
        label: {
          show:true,
          formatter: "{c}%",
          color:'white',
          distance:8
        },
        itemStyle: {
          normal: {
            color: (this as any).colorData[index],
            lineStyle: {
              color: (this as any).colorData[index],
              width: 1
            },
            areaStyle: {
              color: (this as any).showAreaColor
                ? new echarts.graphic.LinearGradient(0, 1, 0, 0, [
                    {
                      offset: 0,
                      color: "rgba(7,44,90,0.3)"
                    },
                    {
                      offset: 1,
                      color:
                        (this as any).colorData[index] == "red"
                          ? "transparent"
                          : (this as any).colorData[index]
                    }
                  ])
                : "transparent"
            }
          }
        },
        data: item
      };
    });
    (this as any).series.push({
      name: "每日跑步里程",
      type: "line",
      smooth: true,
      symbol: "none",
      symbolSize: 0,
      itemStyle: {
        normal: {
          color: "#fff"
        }
      },
      lineStyle: {
        normal: {
          color: "transparent",
          width: 3
        }
      },

      data: [
        99.5,
        99.5,
        99.5,
        99.5,
        99.5,
        99.5,
        99.5,
        99.5,
        99.5,
        99.5,
        99.5,
        99.5
      ],
      lineSmooth: true,
      markLine: {
        silent: true,
        symbol: "none",
        opacity: 1,
        data: [
          {
            type: "average",
            name: "平均值"
          }
        ],
        precision: 0,
        label: {
          normal: {
            formatter: ""
          }
        },
        lineStyle: {
          normal: {
            color: "red",
            type: "solid",
            width: 0.8
          }
        }
      }
      
    });
    console.error("series-----", (this as any).series);
    this.initBar();
  }
  initBar() {
    this.listenScreen();
    (this as any).myChart = this.$echarts.init(this.$el);

    (this as any).option = {
      title: {
        show: false,
        text: "",
        top: "5%",
        left: "center",
        textStyle: {
          color: "#FFF"
          // align: "center"
        }
      },
      backgroundColor: "transparent",
      grid: {
        left: "5%",
        right: "5%",
        top: "20%",
        bottom: "5%",
        containLabel: true
      },
      tooltip: {
        show: true,
        trigger: "item",
        formatter: "{c}%",
      },
      legend: {
        show: (this as any).showLegend,
        top: "0%",
        right: "5%",
        icon: "none",
        itemWidth: 10,
        itemHeight: 10,
        textStyle: {
          color: "#1bb4f6"
        },
        data: (this as any).legendData
      },
      xAxis: [
        {
          type: "category",
          boundaryGap: false,
          splitNumber: 12,
          interval: 12,
          axisLabel: {
            margin: 16,
            color: "#2ad1d2"
            // fontSize:10
          },
          axisLine: {
            show: true,
            lineStyle: {
              color: "#27b4c2"
            }
          },
          axisTick: {
            show: false
          },
          splitLine: {
            show: false,
            lineStyle: {
              color: "#195384"
            }
          },
          data: (this as any).timeData
        }
      ],
      yAxis: [
        {
          type: "value",
          name: "99.9%",
          nameLocation: "end",
          nameTextStyle: {
            fontSize: 14,
            // fontWeight:'bold',
            align: "left",
            width: 300,
            padding: [0, 0, 0, 1080],
            color: "red"
          },
          nameGap: 2,
          min: 0,
          max: (this as any).max,
          axisLabel: {
            formatter: "{value}",
            margin: 12,
            textStyle: {
              color: "#2ad1d2"
            }
          },
          axisLine: {
            lineStyle: {
              color: "#27b4c2"
            }
          },
          axisTick: {
            show: false
          },
          splitLine: {
            show: false,
            lineStyle: {
              color: "#11366e"
            }
          }
        }
      ],
      series: (this as any).series,
      visualMap: [
        {
          show: false,
          pieces: [{ gte: 0, lte: 99.5, color: "red" }],
          outOfRange: {
            color: "#00FEFD",
            symbolSize: 10,
            colorAlpha: 0.6
          }
        }
      ]
    };
    (this as any).myChart.clear();
    (this as any).myChart.setOption((this as any).option, true, false);
    (this as any).myChart = null;
    (this as any).option = null;
  }
}
