












































import { Component, Vue } from "vue-property-decorator";

@Component
export default class Clock extends Vue {
  currentTime: string = "";
  day: number = 50;
  hour: number = 23;
  minute: number = 59;
  second: number = 50;

  hourStr: String = "";
  minuteStr: String = "";
  secondStr: String = "";
  timer: any = null;
  refreshTime() {
    clearTimeout(this.timer);
    // document.getElementById("second").classList.add("animate");
    this.second++;
    // setTimeout(() => {
    //   document.getElementById("second").classList.remove("animate");
    // }, 1000);
    if (this.second == 60) {
      document.getElementById("minute").classList.add("animate");
      this.minute++;
      setTimeout(() => {
        document.getElementById("minute").classList.remove("animate");
      }, 1000);
      this.second = 0;
      if (this.minute == 60) {
        document.getElementById("hour").classList.add("animate");
        this.hour++;
        setTimeout(() => {
          document.getElementById("hour").classList.remove("animate");
        }, 1000);
        this.minute = 0;
        if (this.hour == 24) {
          document.getElementById("day").classList.add("animate");
          this.day++;
          setTimeout(() => {
            document.getElementById("day").classList.remove("animate");
          }, 1000);
          this.hour = 0;
        }
      }
    }
    if (this.second < 10) {
      this.secondStr = "0" + this.second;
    } else {
      this.secondStr = String(this.second);
    }
    if (this.minute < 10) {
      this.minuteStr = "0" + this.minute;
    } else {
      this.minuteStr = String(this.minute);
    }
    if (this.hour < 10) {
      this.hourStr = "0" + this.hour;
    } else {
      this.hourStr = String(this.hour);
    }
    this.timer = setTimeout(this.refreshTime, 1000);
  }
  initData(time: any) {
    ["day", "hour", "minute", "second"].forEach(item => {
      this[item] = time[item];
    });
  }
  mounted() {
    this.refreshTime();
  }
}
