


























import { Component, Vue, Prop, Watch } from "vue-property-decorator";
class alarmData {
  public level: number;
  public faultTime: string;
  public deviceName: string;
  public alarmDesc: string;
}
@Component
export default class myScrollTest extends Vue {
  // @Prop(Array) message: Array<alarmData> | undefined;
  // @Watch("message", { deep: true })
  // onMessageChanged(newValue: Array<alarmData>, oldValue: Array<alarmData>) {
  //   for (let i = 0; i < newValue.length; i++) {
  //     if (oldValue[i] != newValue[i]) {
  //       this.listData[i] = newValue[i];
  //       this.getData();
  //     }
  //   }
  // }
  isShowSpin: boolean = false;
  animate: boolean = false;
  listData: Array<alarmData> = [];
  isShow: boolean = true;
  list: Array<alarmData> = [];
  style: any = {};
  timeOut: number = 2000;

  mounted() {
    // setTimeout(this.refreshData,2000);
  }
  initItems() {
    // this.listData = this.message;
  }
  //初始化滚动事件
  initScrollEvent() {
    var ele = document.getElementById("con1");
    ele.addEventListener(
      "mouseover",
      () => {
        ele.style.animationPlayState = "paused";
      },
      false
    );
    ele.addEventListener("animationstart", this.animationStart);
    ele.addEventListener("animationend", this.animationEnd);
    ele.addEventListener(
      "mouseout",
      () => {
        ele.style.animationPlayState = "running";
      },
      false
    );
  }
  //滚动开始事件，将滚动数组第一个元素push到数组末尾
  animationStart() {
    this.list = JSON.parse(JSON.stringify(this.listData));
    var firstEle = this.list[0];
    this.list.push(firstEle);
    this.listData = JSON.parse(JSON.stringify(this.list));
  }
  //滚动结束事件，将数组第一个元素删除
  animationEnd() {
    this.list.shift();
    this.listData = this.list;
    this.style = {};
    setTimeout(() => {
      this.style = {
        animation: "alarm-scroll " + this.timeOut / 1000 + "s linear both"
      };
    }, this.timeOut);
  }
  //初始化滚动动画
  initscroll() {
    this.style = {
      animation: "alarm-scroll " + this.timeOut / 1000 + "s linear both"
    };
  }
  // exchangeShow(){
  //   this.isShowSpin = true;
  // }
  initData(message: Array<any>) {
    // this.isShowSpin = false;
    if (message.length > 0) {
      this.isShow = false;
      this.listData.splice(0, this.listData.length, ...message);
    } else {
      this.isShow = true;
      this.listData = [];
    }
    console.error("scrollTest页面接收到的告警列表为--", this.listData);
    if (this.listData.length > 8) {
      this.initscroll();
      this.initScrollEvent();
    }
  }

  removeAnimation() {
    this.style = {};
  }
  // refreshData() {
  //   this.getData();
  //   setTimeout(this.refreshData, 100000);
  // }
  getFontColor(level: number) {
    switch (level) {
      case 1:
        return {
          color: "rgba(237, 28, 36,1)"
        };
      case 2:
        return {
          color: "rgba(255, 156, 1)"
        };
      case 3:
        return {
          color: "rgba(222, 210, 4,1)"
        };
      case 4:
        return {
          color: "rgba(3, 207, 123,1)"
        };
      case 5:
        return {
          color: "rgba(24,163,239,1)"
        };
    }
  }
  _getColor(level: number) {
    switch (level) {
      case 1:
        return {
          backgroundColor: "rgba(237, 28, 36,0.8)"
        };
      case 2:
        return {
          backgroundColor: "rgba(255, 156, 0,0.8)"
        };
      case 3:
        return {
          backgroundColor: "rgba(222, 210, 4,0.8)"
        };
      case 4:
        return {
          backgroundColor: "rgba(3, 207, 123,0.8)"
        };
      case 5:
        return {
          backgroundColor: "rgba(24,163,239,0.8)"
        };
    }
  }
  _getBgColor(level: number) {
    switch (level) {
      case 1:
        return {
          borderRightColor: "rgba(237, 28, 36,0.8)"
        };
      case 2:
        return {
          borderRightColor: "rgba(255, 156, 0,0.8)"
        };
      case 3:
        return {
          borderRightColor: "rgba(222, 210, 4,0.8)"
        };
      case 4:
        return {
          borderRightColor: "rgba(3, 207, 123,0.8)"
        };
      case 5:
        return {
          borderRightColor: "rgba(24,163,239,0.8)"
        };
    }
  }
  _getDescBgColor(level: number) {
    switch (level) {
      case 1:
        return {
          background:
            "-webkit-linear-gradient(left, rgba(237, 28, 36,0.8), rgba(237, 28, 36,0.53), rgba(237, 28, 36,0.23), rgba(237, 28, 36,0.13), rgba(237, 28, 36,0)"
        };
      case 2:
        return {
          background:
            "-webkit-linear-gradient(left, rgba(255, 156, 0,0.8), rgba(255, 156, 0, 0.53), rgba(255, 156, 0, 0.23), rgba(255, 156, 0, 0.13), rgba(255, 156, 0, 0))"
        };
      case 3:
        return {
          background:
            "-webkit-linear-gradient(left, rgba(222, 210, 4,0.8), rgba(222, 210, 4,0.53), rgba(222, 210, 4,0.23), rgba(222, 210, 4,0.13), rgba(222, 210, 4,0))"
        };
      case 4:
        return {
          background:
            "-webkit-linear-gradient(left, rgba(3, 207, 123,0.8), rgba(3, 207, 123,0.53), rgba(3, 207, 123,0.23), rgba(3, 207, 123,0.13), rgba(3, 207, 123,0))"
        };
      case 5:
        return {
          background:
            "-webkit-linear-gradient(left, rgba(24,163,239,0.8), rgba(24,163,239, 0.53), rgba(24,163,239, 0.23), rgba(24,163,239, 0.13), rgba(24,163,239, 0))"
        };
    }
  }
}
