




import echarts from 'echarts';
import { Component, Prop, Vue } from "vue-property-decorator";

@Component
export default class Bar extends Vue {
  myChart: any = null;
  option: any = null;

  fontSizeOne: number = 0.28 * (this as any).$rem.rem();
  mounted() {
      var data:Array<any> = [];
      this.initBar(data);
    // this.listenScreen();
    // this.initPie();
  }
  listenScreen() {
    window.addEventListener("resize", () => {
      this.myChart.resize();
      // setTimeout(this.initPie, 50);
    });
  }
  initBar(data: Array<any>) {
    this.listenScreen();
    this.myChart = this.$echarts.init(this.$el);
  
    this.option = {
      backgroundColor:'transparent',
        tooltip: {
          trigger: 'axis',
          axisPointer: { // 坐标轴指示器，坐标轴触发有效
            type: 'shadow' // 默认为直线，可选为：'line' | 'shadow'
          }
        },
        grid: {
          left: '12%',
          right: '4%',
          bottom: '4%',
          top:'16%',
          containLabel: true
        },
         legend: {
        data: ['紧急','重要','次要','警告'],
        right: 10,
        top:12,
        textStyle: {
            color: "#fff"
        },
        itemWidth: 12,
        itemHeight: 10,
        // itemGap: 35
    },
        xAxis: {
          type: 'category',
          data: ['路由器', '交换机', '服务器','软件系统'],
          axisLine: {
              show:true,
            lineStyle: {
              color: 'white'

            }
          },
          axisLabel: {
            // interval: 0,
            // rotate: 40,
            textStyle: {
              fontFamily: 'Microsoft YaHei'
            }
          },
        },

        yAxis: {
          type: 'value',
          // max:'1200',
          axisLine: {
            show: true,
            lineStyle: {
              color: 'white'
            }
          },
          splitLine: {
            show: true,
            lineStyle: {
              color: 'rgba(255,255,255,0.2)'
            }
          },
          axisLabel: {}
        },
        "dataZoom": [{
          "show": false,
          "height": 12,
          "xAxisIndex": [
            0
          ],
          bottom:'8%',
          "start": 10,
          "end": 90,
        //   handleIcon: 'path://M306.1,413c0,2.2-1.8,4-4,4h-59.8c-2.2,0-4-1.8-4-4V200.8c0-2.2,1.8-4,4-4h59.8c2.2,0,4,1.8,4,4V413z',
          handleSize: '110%',
          handleStyle:{
            color:"#d3dee5",

          },
          textStyle:{
            color:"#fff"},
          borderColor:"#90979c"
        }, {
          "type": "inside",
          "show": true,
          "height": 15,
          "start": 1,
          "end": 35
        }],
        series: [{
          name: '紧急',
          type: 'bar',
          barWidth: '15%',
          itemStyle: {
            normal: {
                color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [{
                    offset: 0,
                    color: 'rgba(218,50,37,1)'
                }, {
                    offset: 1,
                    color: 'rgba(218,50,37,0.6)'
                }]),
                barBorderRadius: 12,
            },
          },
          data: data[0]
        },
        {
          name: '重要',
          type: 'bar',
          barWidth: '15%',
          itemStyle: {
            normal: {
                 color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [{
                    offset: 0,
                    color: 'rgba(255,127,39,1)'
                }, {
                    offset: 1,
                    color: 'rgba(255,127,39,0.6)'
                }]),
                barBorderRadius: 12,
            }
            
          },
          data: data[1]
        },
        {
          name: '次要',
          type: 'bar',
          barWidth: '15%',
          itemStyle: {
            normal: {
                color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [{
                    offset: 0,
                    color: 'rgba(255,242,0,1)'
                }, {
                    offset: 1,
                    color: 'rgba(255,242,0,0.6)'
                }]),
                barBorderRadius: 12,
            }
          },
          data: data[2]
        },
        {
          name: '警告',
          type: 'bar',
          barWidth: '15%',
          itemStyle: {
            normal: {
                color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [{
                    offset: 0,
                    color: 'rgba(139,212,10,1)'
                }, {
                    offset: 1,
                    color: 'rgba(139,212,10,0.6)'
                }]),
                barBorderRadius: 11,
            }
          },
          data: data[3]
        }]
    };

    this.myChart.setOption(this.option, true);
  }
}
