







































import echarts from "echarts";
import { Component, Prop, Vue } from "vue-property-decorator";

@Component
export default class Bar extends Vue {
  myChart: any = null;
  option: any = null;

  fontSizeOne: number = 0.28 * (this as any).$rem.rem();
  mounted() {
    var data: Array<any> = [];
    this.initBar(data);
  }
  listenScreen() {
    window.addEventListener("resize", () => {
      this.myChart.resize();
    });
  }
  initBar(data: Array<any>) {
    this.listenScreen();
  }
}
